<template>
  <div>
    <Cabecera 
      titulo="Crear Entidad" 
      iconoTitulo="mdi-office-building" 
      color="primary"
    />
    <v-card>
      <v-card-text>
        <v-text-field 
          v-model="nuevo" 
          label="Registrar nueva entidad"
        ></v-text-field>
        <v-btn
          depressed
          color="primary"
          @click="grabar()"
          :disabled="nuevo===''"
        >
          GRABAR ENTIDAD
        </v-btn>
      </v-card-text>
    </v-card>

    <v-data-table
      dense
      :headers="headers"
      :items="infra"
      class="elevation-1"
      :search="search"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-text-field class="text-xs-center" v-model="search" append-icon="mdi-account-search" label="busqueda" single-line hide-details></v-text-field>
        </v-toolbar>
      </template>          
      <template v-slot:item.actions="{ item }">
        <v-icon small @click="eliminar(item)">
          mdi-delete
        </v-icon>
      </template>
      <template v-slot:no-data>
        SIN REGISTROS
      </template>
    </v-data-table>
  </div>
</template>

<script>
import Cabecera from '../../components/cabecera/Cabecera.vue';
import axios from "axios";
export default {
  name:"Crear",
  components:{
    Cabecera,
  },
  data: () => ({
    search:'',
    headers: [
      { text: 'Entidad', value: 'nombre'},
      { text: 'Acciones', value: 'actions', sortable: false },
    ],
    infra:[],
    nuevo:"",
  }),
  async created () {
    this.$store.commit('SET_LOADING',true );
    let response = await axios.post("/api/listainstitucion");
    this.infra = response.data.instituciones;
    this.$store.commit('SET_LOADING',false );
  },
  methods: {
    async eliminar(item){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/borrarinstitucion",{'id_institucion': item.id});
      this.infra = response.data.instituciones;
      this.$store.commit('SET_LOADING',false );
    },
    async grabar(){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/insertarinstitucion",{'nombre':this.nuevo});
      this.infra = response.data.instituciones;
      this.nuevo= "";
      this.$store.commit('SET_LOADING',false );
    }
  }
}
</script>

