<template>
  <div>
    <v-row>
      <v-col v-for="dato in datos" :key="dato.id" cols="6" md="3">
        <v-card
          outlined
          elevation="2"
          height="100%"
        >
          <v-card-text>
            <v-row>
              <v-col cols="12">
                <v-row>
                  <v-col cols="12" md="2" class="pa-0 mx-0 text-center"> 
                    <v-icon size="50"
                      color="iconocolor"
                      >
                      {{dato.icono}}
                    </v-icon>
                  </v-col>            
                  <v-col class="textocolor--text subtitle-2 flex text-center font-weight-bold py-0 center" cols="12" md="10">
                    <p v-html="dato.titulo"></p>
                    <p class="pa-0 ma-0"  v-html="dato.valor"></p>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>
<script>
export default {
  name:"CardDato",
  props: {
    dato: {
        type:Array
    }
  },
  data: () => ({
    datos: [
      {id:1,color:"deep-orange darken-4",icono:"mdi-city",valor:"0",titulo:"Cantidad de infraestructuras públicas"},
      {id:2,color:"deep-orange darken-4",icono:"mdi-timeline-clock",valor:"0",titulo:"Consumo de energía eléctrica Facturada [kWh]"},
      {id:3,color:"deep-orange darken-4",icono:"mdi-connection",valor:"0",titulo:"Consumo de energía primaria [kWh]"},
      {id:4,color:"deep-orange darken-4",icono:"mdi-cloud",valor:"0",titulo:"Emisiones de CO<sub>2</sub> [t<sub>eq</sub>]"},
      {id:5,color:"deep-orange darken-4",icono:"mdi-currency-usd",valor:"0",titulo:"Importe de Energía Eléctrica [Bs]"},
      {id:6,color:"deep-orange darken-4",icono:"mdi-hammer-screwdriver",valor:"0",titulo:"Superficie construida [m<sup>2</sup>]"},
      {id:7,color:"deep-orange darken-4",icono:"mdi-account-supervisor",valor:"0",titulo:"Personal trabajando en infraestructuras públicas"},
      {id:8,color:"deep-orange darken-4",icono:"mdi-account-supervisor-circle",valor:"0",titulo:"Visitantes en infraestructuras públicas [personas/día]"},
    ],
  }),
  mounted(){
    let formatter = new Intl.NumberFormat('en-US');
    this.datos[0].valor = formatter.format(this.dato[0].infraestructuras);
    this.datos[1].valor = formatter.format(Math.round(this.dato[0].kwh));
    this.datos[2].valor = formatter.format(Math.round(this.dato[0].energia_primaria));
    this.datos[3].valor = formatter.format(Math.round(this.dato[0].consumo_co2));
    this.datos[4].valor = formatter.format(Math.round(this.dato[0].indice_consumo_bs));
    this.datos[5].valor = formatter.format(Math.round(this.dato[0].m2_construidos));
    this.datos[6].valor = formatter.format(Math.round(this.dato[0].personas));
    this.datos[7].valor = formatter.format(Math.round(this.dato[0].visitantes));

  }
}
</script>
<style scope>
  .v-application p {
    margin-bottom: 0px;
  }
</style>