<template>
  <div>
    <v-container fluid>
    <v-row>
      <v-col cols="12" class="py-0 caption">
        <p class="font-weight-bold">Coordenadas Geográficas</p>
      </v-col>
      <template v-if="dlatitud!=null">
        <v-col cols="6" class="py-0">
          <span class="font-weight-bold">Latitud: </span> {{ dlatitud }}
        </v-col>
        <v-col cols="6" class="py-0">
          <span class="font-weight-bold">Longitud: </span> {{ dlongitud }}
        </v-col>
      </template>
    </v-row>
      <v-row>
        <v-col class="pa-0" cols="12">
          <v-card height="300">
            <v-card-text class="pa-0 ma-0">
              <div style="height: 300px; width: 100%">
                <l-map
                  :zoom="zoom"
                  :center="center"
                  :options="mapOptions"
                  style="height: 100%"
                >
                  <l-tile-layer
                      :url="url"
                      :attribution="attribution"
                    />
                   <l-marker v-if="dlatitud!=null" :lat-lng="[dlatitud,dlongitud]" :draggable="true" @move="dragStartHandler">
                    </l-marker>
                </l-map>
              </div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
    <v-row>
      <v-col cols="6" class="pr-0">
        <v-btn color="primary" block small @click="grabar" :disabled="dlatitud==null">
          Grabar posición
        </v-btn>
      </v-col>
      <v-col cols="6" class="pl-0">
        <v-btn color="warning" block small @click="getUserPosition">
          Posición actual
        </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from "axios";
import { LMap, LTileLayer, LMarker, LPopup, LTooltip, LIcon  } from "vue2-leaflet"; 
import { latLng, Icon } from "leaflet"; 
import 'leaflet/dist/leaflet.css';
delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});
export default { 
  name: "Geografico", 
  components: { 
    LMap, LTileLayer, 
    LMarker, 
    LPopup, 
    LTooltip,
    LIcon
  }, 
  props:{
    dato: {
      type: Object
    }
  },
  data() { 
    return { 
      zoom: 15, 
      userLocation: {},
      center: latLng(this.dato.latitud==null?-16.508433858298485:this.dato.latitud, 
        this.dato.longitud==null?-68.143000602722:this.dato.longitud), 
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', 
      attribution: '© OpenStreetMap contributors', 
      mapOptions: { 
        zoomSnap: 0.5 
      }, 
      dlatitud: this.dato.latitud==null?-16.508433858298485:this.dato.latitud,
      dlongitud: this.dato.longitud==null?-68.143000602722:this.dato.longitud,
    }; 
  }, 
  
  methods: { 
    dragStartHandler: function (e) {
      var latlng = e.target.getLatLng();
      console.log(lating);
      this.dlatitud = latlng.lat;
      this.dlongitud = latlng.lng;
    },
    async grabar(){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/modificargeo", {
        'latitud':this.dlatitud,
        'longitud':this.dlongitud,
        'id':this.dato.id,
      });
      this.infraestructura = response.data.infraestructura[0];
      this.$parent.infraestructura = this.infraestructura;
      this.$store.commit('SET_LOADING',false );
    },
    async getUserPosition() {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(pos => {
          this.center = latLng(pos.coords.latitude,pos.coords.longitude);
          this.dlatitud = pos.coords.latitude;
          this.dlongitud = pos.coords.longitude;
          this.zoom = 15; 
          console.log(pos.coords.latitude);
        });
      }
    }
  },
};
</script>
