<template>
  <div>
    <v-row class="px-5" align="center">
      <v-col class="d-flex" cols="6">
        <v-select
          v-model="selectCiudad"
          :items="ciudades"
          label="Ciudades"
          @input="onCiudad($event)"
          multiple
        >
          <template v-slot:selection="{ item, index }">
            <v-chip v-if="index === 0">
              <span>{{ item }}</span>
            </v-chip>
            <span
              v-if="index === 1"
              class="grey--text caption"
            >
              (+{{ selectCiudad.length - 1 }} otras)
            </span>
          </template>
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="6">
        <v-select
          v-model="selectDependencia"
          :items="dependencias"
          label="Dependencias"
          @input="onDependencia($event)"
          multiple
        >
          <template v-slot:selection="{ item, index }">
            <v-chip v-if="index === 0">
              <span>{{ item }}</span>
            </v-chip>
            <span
              v-if="index === 1"
              class="grey--text caption"
            >
              (+{{ selectDependencia.length - 1 }} otras)
            </span>
          </template>
        </v-select>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name:"FiltroHistorico",
  props: {
    datos:{
      type: Object
    },
  },
  data: ()=>({
    ciudades: null,
    selectCiudad: [],
    dependencias: null,
    selectDependencia: [],
  }),
  created() {
    this.ciudades = this.datos.data.ciudad;
    this.selectCiudad = this.datos.data.ciudad;
    this.dependencias = this.datos.data.dependencia;
    this.selectDependencia = this.datos.data.dependencia;
  },
  methods:{
    onDependencia(e){
      this.$emit('on_dependencia',this.selectDependencia);
    },
    onCiudad(e){
      this.$emit('on_ciudad', this.selectCiudad);
    },
  }
}
</script>