import Vue from 'vue'
import Vuex from 'vuex'
import auth from './auth'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    loading: false,
    iconMenu: false,
    drawerState: false,
    nivel : 'S/N',
    titulo : 'VARIABLES E INDICADORES COMPARATIVOS HISTÓRICOS'
  },
  mutations: {
    SET_LOADING(state,bool){
      state.loading = bool;
    },
    toggleDrawerState (state, data) {
      state.drawerState = data
    },
    SET_TITULO(state,titulo){
      state.titulo = titulo;
    },
  },
  actions: {

  },
  getters:{
    drawerState: (state) => state.drawerState,
    getTitulo: (state) => state.titulo
  },
  modules: {
    auth
  }
})
