<template>
  <div>
    <v-row>
      <v-col cols="3" class="py-4 caption">
        Tipo de Infraestructura
        <v-radio-group v-model="infraestructura.id_tipo_infraestructura" class="py-0">
          <v-radio label="Casa" :value="1"></v-radio>
          <v-radio label="Conventillo" :value="2"></v-radio>
          <v-radio label="Edificio" :value="3"></v-radio>
          <v-radio label="Otros" :value="4"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-4 caption">
        La fachada exterior presenta fisuras de más de 3 cm
        <v-radio-group v-model="infraestructura.fachada_fisura" class="py-0">
          <v-radio label="Si" :value="true"></v-radio>
          <v-radio label="No" :value="false"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-4 caption">
        La pintura de la fachada presenta deterioro?
        <v-radio-group v-model="infraestructura.id_fachada" class="py-0">
          <v-radio label="Si" :value="3"></v-radio>
          <v-radio label="No" :value="1"></v-radio>
          <v-radio label="No Tiene" :value="2"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-4 caption">
        Esta institución utiliza la infraestructura
        <v-radio-group v-model="infraestructura.id_utiliza" class="py-0">
          <v-radio label="Completa" :value="1"></v-radio>
          <v-radio label="Una Seccion / pisos" :value="2"></v-radio>
        </v-radio-group>
      </v-col>
      <!--
      <v-col cols="12" class="py-0 caption">
        Nombre la unidad(es)/bloque(s)/UE(s),etc
      </v-col>
      <v-col cols="4" class="py-0">
        <v-text-field
          v-model="infraestructura.nombre_bloque_1"
        ></v-text-field>
      </v-col>
      <v-col cols="4" class="py-0">
        <v-text-field
          v-model="infraestructura.nombre_bloque_2"
        ></v-text-field>
      </v-col>
      <v-col cols="4" class="py-0">
        <v-text-field
          v-model="infraestructura.nombre_bloque_3"
        ></v-text-field>
      </v-col>
      -->
      <v-col cols="3" class="py-0 caption">
        Propiedad de la Infraestructura
        <v-radio-group v-model="infraestructura.id_propiedad" class="py-0">
          <v-radio label="Alquilada" :value="1"></v-radio>
          <v-radio label="Anticrético" :value="2"></v-radio>
          <v-radio label="Comodato" :value="3"></v-radio>
          <v-radio label="Otros" :value="4"></v-radio>
          <v-radio label="Propia" :value="5"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-0 caption">
        Cantidad de personas que trabajan en la infraestructura
        <v-text-field
          v-model="infraestructura.personas"
          label="Personas"
        ></v-text-field>
      </v-col>
      <v-col cols="3" class="py-0 caption">
        Cantidad de personas que visitan la institución día/semana
        <v-text-field
          v-model="infraestructura.visitas"
          label="Visitas"
        ></v-text-field>
        <v-radio-group v-model="infraestructura.dia_semana" class="py-0">
          <v-radio label="Día" value="Día"></v-radio>
          <v-radio label="Semana" value="Semana"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-0 caption">
        Complementos en la infraestructura. Otros tipos de energía utilizados
        <v-radio-group v-model="infraestructura.id_gas_natural" class="py-0" row label="GAS natural">
          <v-radio label="No" :value="1"></v-radio>
          <v-radio label="Si" :value="2"></v-radio>
        </v-radio-group>
        <v-radio-group v-model="infraestructura.id_energia_solar" class="py-0" row label="Energía solar">
          <v-radio label="No" :value="1"></v-radio>
          <v-radio label="Si" :value="2"></v-radio>
        </v-radio-group>
        <v-radio-group v-model="infraestructura.id_otra_energia" class="py-0" row label="Otro tipo energía">
          <v-radio label="No" :value="1"></v-radio>
          <v-radio label="Si" :value="2"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="12" class="py-0 caption">
        Complementos en la infraestructura. Comodidades adicionales
      </v-col>
      <v-col cols="3" class="py-0">
        <v-radio-group v-model="infraestructura.id_calefaccion" class="py-0" label="Calefacción">
          <v-radio label="No tiene" :value="1"></v-radio>
          <v-radio label="Si, a Energía Eléctrica" :value="2"></v-radio>
          <v-radio label="Si, a gas" :value="3"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-0">
        <v-radio-group v-model="infraestructura.id_agua_caliente" class="py-0" label="Agua caliente">
          <v-radio label="No tiene" :value="1"></v-radio>
          <v-radio label="Otro" :value="2"></v-radio>
          <v-radio label="Si, a Energía Eléctrica" :value="3"></v-radio>
          <v-radio label="Si, a gas" :value="4"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-0">
        <v-radio-group v-model="infraestructura.id_ventilacion" class="py-0" label="Ventilación">
          <v-radio label="Si" :value="2"></v-radio>
          <v-radio label="No tiene" :value="1"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="3" class="py-0">
        <v-radio-group v-model="infraestructura.id_aire_acondicionado" class="py-0" label="Aire Acondicionado">
          <v-radio label="No tiene" :value="1"></v-radio>
          <v-radio label="Otro" :value="2"></v-radio>
          <v-radio label="Si, frio/ calor" :value="3"></v-radio>
          <v-radio label="Si, solo frio" :value="4"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="4" class="py-0 caption">
        Aproximadamente cuantos metros cuadrados tiene este bloque
        <v-text-field
          v-model="infraestructura.metros_construidos"
        ></v-text-field>
      </v-col>
      <v-col cols="4" class="py-0 caption">
        Aproximadamente en que año fue construida esta infraestructura?
        <v-radio-group v-model="infraestructura.id_construccion" class="py-0">
          <v-radio label="Antes de 1970" :value="1"></v-radio>
          <v-radio label="Entre 1970 y 2000" :value="2"></v-radio>
          <v-radio label="No saben" :value="3"></v-radio>
          <v-radio label="Posterior a 2000" :value="4"></v-radio>
          <v-radio label="Sin información" :value="5"></v-radio>
        </v-radio-group>
      </v-col>
      <v-col cols="4" class="py-0 caption">
        Se hicieron remodelaciones?
        <v-radio-group v-model="infraestructura.id_remodelacion" class="py-0">
          <v-radio label="Si" :value="2"></v-radio>
          <v-radio label="No" :value="1"></v-radio>
        </v-radio-group>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" class="text-center">
        <v-btn
          color="primary"
          @click="grabar"
        >
        Grabar
        </v-btn>
      </v-col>
    </v-row>
    <br><br><br>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props:{
    dato: {
      type: Object
    }
  },
  data(){
    return {
      infraestructura:{
        id_tipo_infraestructura:1,
        fachada_fisura:true,
        id_fachada:3,
        id_utiliza:1,
        id_propiedad:1,
        dia_semana:"Día",
        id_gas_natural:1,
        id_energia_solar:1,
        id_otra_energia:1,
        id_calefaccion:1,
        id_agua_caliente:1,
        id_ventilacion:2,
        id_aire_acondicionado:1,
        id_construccion:1,
        id_remodelacion:2,
        metros_construidos:0,
        visitas:0,
        personas:0,
        id:null,
        id_infraestructura:null
      },
    }
  },
  async mounted(){
    this.$store.commit('SET_LOADING',true );
    let response = await axios.post("/api/datosbloque",{'id':this.dato.id});
    if(typeof response.data.institucion[0] === 'undefined')
      this.infraestructura.id_infraestructura = this.dato.id;
    else
      this.infraestructura = response.data.institucion[0];
    this.$store.commit('SET_LOADING',false );
  },
  methods:{
     async grabar(){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/grabarbloque",{
        'infraestructura':this.infraestructura
      });
      this.infraestructura = response.data.institucion[0];
      this.$store.commit('SET_LOADING',false );
      //console.log(this.infraestructura);
    }
  }
}
</script>

