<template>
  <div>
    <v-btn
      class="ml-5 mr-2 my-3"
      :disabled="medidor.medidor=='' || medidor.numero_cliente==''"
      small
      color="primary"
      @click="saveMedidor"
    >
      <v-icon dark>
        mdi-content-save
      </v-icon> Grabar
    </v-btn>
    <v-btn
      class="mx-0 my-3"
      small
      color="error"
      @click="cancelarMedidor"
    >
      <v-icon dark>
        mdi-close
      </v-icon> Cancelar
    </v-btn>
    <v-row>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="medidor.medidor"
          label="N° medidor"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="medidor.numero_cliente"
          label="N° cliente"
        ></v-text-field>
      </v-col>
      <v-col cols="4" class="py-0">
        <v-text-field
          v-model="medidor.cliente"
          label="Cliente"
        ></v-text-field>
      </v-col>
      <v-col cols="4" class="py-0">
        <v-text-field
          v-model="medidor.direccion"
          label="Dirección"
        ></v-text-field>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name:"NuevoMedidor",
  props:{
    id_infraestructura: {
      type: Number
    }
  },
  data(){
    return {
      medidor: {
        medidor : "",
        numero_cliente: "",
        cliente: "",
        direccion: "",
        id_infraestructura: this.id_infraestructura
      }
    }
  },
  methods: {
    async saveMedidor(){
      this.$store.commit('SET_LOADING',true );
      await axios.post("/api/insertmedidor",{
        'medidor': this.medidor.medidor,
        'numero_cliente': this.medidor.numero_cliente,
        'cliente': this.medidor.cliente,
        'direccion': this.medidor.direccion,
        'id_infraestructura': this.medidor.id_infraestructura,
      });
      this.$store.commit('SET_LOADING',false );
      this.$emit('on_close');
    },
    cancelarMedidor(){
      this.$emit('on_close');
    }
  }
}
</script>

