<template>
  <div>
    <Cabecera 
      :titulo="titulo" 
      iconoTitulo="mdi-chart-timeline" 
      :subtitulo="mes +' - '+ gestion"
      iconoSubtitulo="mdi-calendar-month"
      color="primary"
    />

    <v-container fluid>
      <v-row align="center" justify="space-around" class="pb-5">
        <v-btn :disabled="componente=='Infraestructura'?true:false" text color="light-blue darken-4" @click="cambiar('Infraestructura')">
          Infraestructura
        </v-btn>
        <v-btn :disabled="componente=='Edificio'?true:false" text color="light-blue darken-4" @click="cambiar('Edificio')">
          Nombre edificio
        </v-btn>
      </v-row>
      <template>
        <component :is="componente" v-bind="currentProperties"/><br><br><br>
      </template>
    </v-container>

    
  </div>
</template>

<script>
import Cabecera from '../components/cabecera/Cabecera.vue';
import Infraestructura from '../views/indicadores/Infraestructura.vue';
import Edificio from '../views/indicadores/Edificio.vue';
import axios from "axios";

export default {
  name:"Indicadores",
  components:{
    Cabecera,
    Infraestructura,
    Edificio,
  },
  data: () => ({
    search:'',
    mes: '',
    gestion: '',
    componente: "Infraestructura",
    indicadores: [],
    indicadores_edificio: [],
    titulo: "Indicadores por institución",
  }),
  async mounted () {
    this.$store.commit('SET_LOADING',true );
    let response = await axios.post("/api/indicadores");
    this.gestion = response.data.data.gestion;
    this.mes = response.data.data.mes;
    console.log(response.data.data);
    this.indicadores = response.data.data.indicadores;
    this.indicadores_edificio = response.data.data.indicadores_edificio;
    this.$store.commit('SET_LOADING',false );
  },
  computed: {
    currentProperties: function() {
      if(this.componente === 'Infraestructura'){
        //this.titulo = "Indicadores por institución";
        return { pindicador:this.indicadores }
      }
      else{
        //this.titulo = "Indicadores por infraestructura";
        return { pindicador:this.indicadores_edificio }
      }
    }
  },
  methods:{
    cambiar(indicador){
      this.componente = indicador
    },
  }
}
</script>

