<template>
  <div>
    <Cabecera 
      titulo="Administrar infraestructuras" 
      iconoTitulo="mdi-domain" 
      color="primary"
    />
    <template v-if="!editar">
      <v-col cols="12" class="caption px-5">
        Esta sección permite administrar la información de la(s) infraestructura(s), siendo posible actualizar la información existente. Las modificaciones en la base de datos, serán actualizadas cada fin de mes.
      </v-col>      
      <v-data-table
        dense
        :headers="headers"
        :items="infraestructuras"
        class="elevation-1"
        :search="search"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-text-field class="text-xs-center" v-model="search" append-icon="mdi-account-search" label="busqueda" single-line hide-details></v-text-field>
            <v-spacer></v-spacer>
            <v-btn
              color="primary"
              dark
              small
              class="mb-2"
              @click="nuevo"
            >
              Nuevo
            </v-btn>
          </v-toolbar>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
        </template>
        <template v-slot:no-data>
          SIN REGISTROS
        </template>
      </v-data-table>
    </template>
    <template v-else>
      <v-toolbar flat>
        <h3>{{ titulo }}</h3>
        <v-spacer></v-spacer>
        <v-btn
          color="success"
          small
          dark
          class="mb-2"
          @click="cancelar()"
        >
          Volver
        </v-btn>
      </v-toolbar>
      <v-container fluid>
        <v-row align="center" justify="space-around" class="py-2">
          
          <v-btn :disabled="componente=='DatosInfraestructura'?true:false" text color="light-blue darken-4" @click="cambiar('DatosInfraestructura')">
            Datos infraestructura
          </v-btn>
          <template v-if="Object.keys(infraestructura).length">
            <v-btn :disabled="componente=='DatosInstitucion'?true:false" text color="light-blue darken-4" @click="cambiar('DatosInstitucion')">
              Datos entidad
            </v-btn>
            <v-btn :disabled="componente=='DatosBloque'?true:false" text color="light-blue darken-4" @click="cambiar('DatosBloque')">
              Datos bloque
            </v-btn>
            <v-btn :disabled="componente=='Medidores'?true:false" text color="light-blue darken-4" @click="cambiar('Medidores')">
              Medidores
            </v-btn>
            <v-btn :disabled="componente=='UbicacionGeografica'?true:false" text color="light-blue darken-4" @click="cambiar('UbicacionGeografica')">
              Ubicación geográfica
            </v-btn>
          </template>
        </v-row>
        <template>
          <component :is="componente" v-bind="currentProperties"/>
        </template>
      </v-container>
    </template>
  </div>
</template>

<script>
import Cabecera from '../../components/cabecera/Cabecera.vue';
import DatosInfraestructura from '../infraestructura/DatosInfraestructura.vue';
import UbicacionGeografica from '../infraestructura/UbicacionGeografica.vue';
import DatosBloque from '../infraestructura/DatosBloque.vue';
import DatosInstitucion from '../infraestructura/DatosInstitucion.vue';
import Medidores from '../infraestructura/Medidores.vue';
import axios from "axios";
export default {
  name:"Infraestructura",
  components:{
    Cabecera,
    DatosInfraestructura,
    UbicacionGeografica,
    DatosBloque,
    DatosInstitucion,
    Medidores,
  },
  data: () => ({
      search:'',
      headers: [
        { text: 'Entidad', value: 'nombre_infraestructura'},
        { text: 'Edificio', value: 'nombre_edificio' },
        { text: 'Ciudad', value: 'ciudad' },
        { text: 'Editar', value: 'actions', sortable: false },
      ],
      infraestructuras: [],
      infraestructura: {},
      editar:false,
      titulo:"Modificar infraestructura",
      componente: "DatosInfraestructura",
    }),

    async created () {
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/infraestructura");
      this.infraestructuras = response.data.data;
      this.$store.commit('SET_LOADING',false );
    },

    methods: {
      editItem(item){
        this.editar = true;
        this.infraestructura = item;
        this.titulo = "Modificar infraestructura";
      },
      nuevo(){
        this.editar = true;
        this.infraestructura = {};
        this.titulo = "Nueva infraestructura";
      },
      async cancelar(){
        this.editar = false;
        this.infraestructura = {};
        this.$store.commit('SET_LOADING',true );
        let response = await axios.post("/api/infraestructura");
        this.infraestructuras = response.data.data;
        this.$store.commit('SET_LOADING',false );
        this.componente = "DatosInfraestructura";
      },
      cambiar(indicador){
        this.componente = indicador
      },
    },
    computed: {
      currentProperties: function() {
          return { dato: this.infraestructura }
      }
    },
}
</script>

