<template>
  <div>
    <v-card  class="ma-0 pa-0">
      <v-card-title class="graficas white--text caption px-2 py-1" v-if="reporte">
        <span v-html="titulo"></span>
      </v-card-title>
      <v-card-text class="pa-2">
        <div>
          <apexchart type="line" :height="alto" :options="chartOptions" :series="series"></apexchart>
        </div>
      </v-card-text>
    </v-card>
    {{ dibujar }}
  </div>  
</template>
<script>
export default {
  name:"LineGraph",
  props: {
    inicial:{
      type: Array
    },
    final:{
      type: Array
    },
    titulo:{
      type: String
    },
    tituloGrafico:{
      type: String
    },
    tituloX:{
      type: String
    },
    tituloY:{
      type: String
    },
    alto:{
      type: Number,
      default: 300
    },
    reporte:{
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      series:[ {
        name: "Global",
        data: this.inicial.map(value=>value.serie1)
      },{
        name: "Selección",
        data: this.final.map(value=>value.serie2)
      }],
      chartOptions: {
        chart: {
          height: 250,
          type: 'line',
          dropShadow: {
            enabled: true,
            color: '#000',
            top: 18,
            left: 7,
            blur: 10,
            opacity: 0.2
          },
          toolbar: {
            show: false
          }
        },
        //colors: ["#00ff00","#ff6000"],
        dataLabels: {
          enabled: true,
          /*formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
              return formatter.format(val);
          },*/
        },
        stroke: {
          curve: 'smooth'
        },
        grid: {
          borderColor: '#e7e7e7',
          row: {
            colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
            opacity: 0.5
          },
        },
        markers: {
          size: 1
        },
        xaxis: {
          categories: this.inicial.map(value=>value.categoria),
          title: {
            text: this.tituloX
          }
        },
        yaxis: {
          title: {
            text: this.tituloY
          },
          labels: {
            show: true,
            formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
                return formatter.format(parseFloat(val).toFixed(1));
            },
          },
        },
        legend: {
          position: 'top',
          horizontalAlign: 'right',
          floating: true,
          offsetY: -25,
          offsetX: -5
        }
      },
    }
  },
  computed: {
    dibujar: function(){
      this.series = [ {
        name: "Global",
        data: this.inicial.map(value=>value.serie1)
      },{
        name: "Selección",
        data: this.final.map(value=>value.serie2)
      }];
      this.chartOptions = {
        chart: {
          height: 250,
          type: 'line',
          dropShadow: {
            enabled: true,
            color: '#000',
            top: 18,
            left: 7,
            blur: 10,
            opacity: 0.2
          },
          toolbar: {
            show: false
          }
        },
        colors: ["#00ff00","#ff6000"],
        dataLabels: {
          enabled: true,
          formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
              return formatter.format(val);
          },
        },
        stroke: {
          curve: 'smooth'
        },
        grid: {
          borderColor: '#e7e7e7',
          row: {
            colors: ['#f3f3f3', 'transparent'], // takes an array which will be repeated on columns
            opacity: 0.5
          },
        },
        markers: {
          size: 1
        },
        xaxis: {
          categories: this.inicial.map(value=>value.categoria),
          title: {
            text: this.tituloX
          }
        },
        yaxis: {
          labels: {
            show: true,
            formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
              return formatter.format(parseFloat(val).toFixed(1));
            },
          },
          title: {
            text: this.tituloY
          },
        },
        legend: {
          position: 'top',
          horizontalAlign: 'right',
          floating: true,
          offsetY: -25,
          offsetX: -5
        }
      };
    }
  }
}
</script>