<template>
  <div>
    <v-card  class="ma-0 pa-0">
    <v-card-title class="graficas white--text caption px-2 py-1" v-if="reporte">
      {{ titulo }}
    </v-card-title>
    <v-card-text class="pa-2">
        <apexchart type="bar" :height="alto" :options="chartOptions" :series="series"></apexchart>
    </v-card-text>
  </v-card>
  {{ dibujar }}
  </div>  
</template>
<script>
export default {
  name:"BarVertical",
  props: {
    datos:{
      type: Array
    },
    titulo:{
      type: String
    },
    alto:{
      type: Number,
      default: 200
    },
    reporte:{
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      series:[{
        name: 'Infraestructura',
        data: this.datos.map(item => item.categorias),
      }],
      chartOptions: {
        chart: {
          type: 'bar',
          toolbar: {
            show: false,
          },
          width: '100%'
        },
        plotOptions: {
          bar: {
            borderRadius: 5,
            dataLabels: {
              position: 'top', // top, center, bottom
            },
            distributed: true,
          }
        },
        legend: {
          show: false
        },
        dataLabels: {
          enabled: true,
          formatter: function (val) {
            return val;
          },
          offsetY: -20,
          style: {
            fontSize: '12px',
            colors: ["#385F73"]
          }
        },
        
        xaxis: {
          categories: this.datos.map(item => item.datos),
          position: 'bottom',
          labels: {
            style: {
              fontSize: this.datos.tamanio?this.datos.tamanio:'12px'
            }
          }
        },
        yaxis: {
          labels: {
            show: true,
            formatter: function (val) {
              return val;
            }
          }
        
        },
      },
    }
  },
  computed: {
    dibujar: function(){
      this.series = [{
        name: 'Infraestructura',
        data: this.datos.map(item => item.categorias),
      }];
      this.chartOptions = {
        //colors : ['#4d3a96', '#4576b5'],
        chart: {
          type: 'bar',
          toolbar: {
            show: false,
          },
          width: '100%'
        },
        //colors: ['#E91E63', '#66DA26', '#546E7A', '#E91E63'],
        plotOptions: {
          bar: {
            borderRadius: 5,
            dataLabels: {
              position: 'top', // top, center, bottom
            },
            distributed: true,
          }
        },
        legend: {
          show: false
        },
        dataLabels: {
          enabled: true,
          formatter: function (val) {
            return val;
          },
          offsetY: -20,
          style: {
            fontSize: '12px',
            colors: ["#385F73"]
          }
        },
        
        xaxis: {
          categories: this.datos.map(item => item.datos),
          position: 'bottom',
          labels: {
            style: {
              fontSize: this.datos.tamanio?this.datos.tamanio:'12px'
            }
          }
        },
        yaxis: {
          labels: {
            show: true,
            formatter: function (val) {
              return val;
            }
          }
        
        },
      };
    }
  }
}
</script>