import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Historico from '../views/Historico.vue'
import Dashboard from '../views/Dashboard.vue'
import Geografico from '../views/Geografico.vue'
import Benchmark from '../views/Benchmark.vue'
import Usuario from '../views/Usuario.vue'
import Login from '../views/Login.vue'
import Indicadores from '../views/Indicadores.vue'
import Crear from '../views/institucion/Crear.vue'
import Infraestructura from '../views/infraestructura/Infraestructura.vue'
import Contrasena from '../views/Contrasena.vue'
import store from '../store'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard
  },
  {
    path: '/historico',
    name: 'Historico',
    component: Historico
  },
  {
    path: '/geografico',
    name: 'Geografico',
    component: Geografico
  },
  {
    path: '/benchmark',
    name: 'Benchmark',
    component: Benchmark,
    meta: { requiresAuth: true }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/usuario',
    name: 'Usuario',
    component: Usuario,
    meta: { requiresAuth: true }
  },
  {
    path: '/infraestructura',
    name: 'Infraestructura',
    component: Infraestructura,
    meta: { requiresAuth: true }
  },
  {
    path: '/modificar',
    name: 'Contrasena',
    component: Contrasena,
    meta: { requiresAuth: true }
  },
  {
    path: '/indicadores',
    name: 'Indicadores',
    component: Indicadores,
    meta: { requiresAuth: true }
  },
  {
    path: '/crearinfraestructura',
    name: 'Crearinfraestructura',
    component: Crear,
    meta: { requiresAuth: true }
  },
  {
    path: '/about',
    name: 'About',
    meta: { requiresAuth: true },
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next)=>{
  let requiresAuth = to.matched.some(record=>record.meta.requiresAuth)
  let isLogged = store.getters['auth/isLoggedIn']
  if(! requiresAuth && isLogged && to.path === '/login'){
    next('/')
  }
  if(requiresAuth && !isLogged){
    next('/login')
  }
  else{
    next()
  }
})

export default router