<template>
  <v-card
    class="mx-auto"
    width="256"
    tile
  >
    <v-navigation-drawer
      v-model="drawerState"
      app
      color="white"
    >
      <v-list-item>
        <v-list-item-content>
          <!--
          <v-list-item-title align="center" class="py-5">
            <v-img
              src="@/assets/realdata.jpg"
              max-width="230"
            >
            </v-img>
          </v-list-item-title>
          -->
        </v-list-item-content>
      </v-list-item>
      <template v-if="user">
        <v-list-item >
            <v-list-item-content>
              <v-list-item-title class="title">
                {{ user.name }}
              </v-list-item-title>
              <v-list-item-subtitle>{{ user.email }}</v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>
      </template>
      <v-divider></v-divider>

      <MenuPrivado v-if="user" :usuario="user"></MenuPrivado>
      <MenuPublico v-else></MenuPublico>
    </v-navigation-drawer>
    <v-app-bar 
      app 
      dense
      color="white"
      py-2
    >
      <v-app-bar-nav-icon @click="drawerState = !drawerState"></v-app-bar-nav-icon>
      <v-toolbar-title class="secondary--text title font-weight-bold flex text-center hidden-sm-and-down">
        PLATAFORMA DE MONITOREO DE INDICADORES DE CONSUMO ENERGÉTICO 
        <br class="titulo"> 
        EN INFRAESTRUCTURAS PÚBLICAS
      </v-toolbar-title>
      <template v-if="user" class="hidden-sm-and-down">
        <v-spacer></v-spacer>
        <v-icon class="mr-2">mdi-account</v-icon>
        <div class="caption">{{ user.email }}</div>
      </template>
    </v-app-bar>
  </v-card>
</template>

<script>
  import MenuPrivado from './cabecera/MenuPrivado'
  import MenuPublico from './cabecera/MenuPublico'
  import { mapState } from 'vuex'
  export default {
    components:{
      MenuPrivado,
      MenuPublico
    },
    name: "MenuGiz",
    computed: {
      ...mapState('auth', ['logged', 'user']),
      drawerState: {
        get () { 
          return this.$store.getters.drawerState 
        },
        set (v) { 
          return this.$store.commit('toggleDrawerState', v) 
        }
      }
    },
  }
</script>
<style>
  br.titulo {
     content: " ";
     display: block;
     margin: -8px 0;
  }
</style>