<template>
  <div>
    <template>
      <v-row class="px-5">
        <v-card flat tile width="100%" class="barracolor mb-5">
          <v-card-text class="white--text subtitle-1 d-flex justify-space-between py-2">
            1. CONSUMO DE ENERGÍA ELÉCTRICA FACTURADA
          </v-card-text>
        </v-card>        
        <v-row>
            <v-col cols="12" class="caption px-5">
              La información disponible de esta sección corresponde al consumo de energía eléctrica de los meses seleccionados. En la columna “Promedios globales”, se encuentran los datos de las variables del total de infraestructuras. En la columna “Promedios según filtro”, se encuentran los datos de las variables en función a los filtros seleccionados según ciudad, dependencia y/o mes, y finalmente el “% de variación según filtro” presenta la variación de la selección filtrada respecto al total. Las fórmulas utilizadas se detallan en el manual de usuario.
            </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-text class="py-2 caption black--text">
                <GraficoTabla :totales="totales" :cabecera="cabecera" />
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12">
            <BarMultiVertical 
              :inicial="inicialGrafico1" 
              :final="finalGrafico1" 
              titulo="Consumo histórico de energía eléctrica" 
              tituloGrafico="Consumo mensual"
              tituloX="Mes - Gestión"
              tituloY="kWh"
            />
              <v-col cols="12" class="caption px-5">
                El gráfico de barras muestra los datos de la sumatoria del consumo de energía mensual del total de las infraestructuras (global), y, de la selección realizada.
              </v-col>
          </v-col>
          <v-row>
        </v-row>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico2" 
              :final="finalGrafico2" 
              titulo="Consumo promedio comparativo de energía eléctrica" 
              tituloGrafico="Consumo promedio mensual"
              tituloX="Mes - Gestión"
              tituloY="kWh (promedio)" />
              <v-col cols="12" class="caption px-5">
                El gráfico de líneas muestra el consumo de energía promedio mensual del total de las infraestructuras (global), y, de la selección realizada. 
              </v-col>
          </v-col>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico3" 
              :final="finalGrafico3" 
              titulo="Variación porcentual del Consumo de energía eléctrica Facturada" 
              tituloGrafico="Variación porcentual mensual"
              tituloX="Mes - Gestión"
              tituloY="Porcentaje" />
              <v-col cols="12" class="caption px-5">
                El gráfico de líneas muestra el incremento o decremento de los promedios de consumo de energía del total de las infraestructuras (global), y, de la selección realizada.
              </v-col>
          </v-col>
        </v-row>      
      </v-row>
      <template>
          <v-row>
            <v-col cols="auto">
              <v-dialog
                transition="dialog-bottom-transition"
                scrollable
                max-width="600px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    class="px-6"
                    small
                    color="primary"
                    v-bind="attrs"
                    v-on="on"
                  >Reporte</v-btn>
                </template>
                <template v-slot:default="dialog">
                  <v-card>
                    <v-card-text>
                      <div v-show="true" style="width:1050px" ref="documento1">
                        <ReporteIndicador1 
                          :totales="totales" :cabecera="cabecera"
                          :inicialGrafico1="inicialGrafico1" 
                          :finalGrafico1="finalGrafico1" 
                          :inicialGrafico2="inicialGrafico2" 
                          :finalGrafico2="finalGrafico2" 
                          :inicialGrafico3="inicialGrafico3" 
                          :finalGrafico3="finalGrafico3" 
                          :tituloImpresion="titulo"
                        />
                      </div>
                    </v-card-text>
                    <v-card-actions class="justify-end">
                      <v-btn
                        outlined
                        color="primary"
                        @click="imprimir"
                      >
                      <v-icon left>
                        mdi-file-pdf
                      </v-icon>
                      Generar PDF</v-btn>
                      <v-btn
                        outlined
                        color="error"
                        @click="dialog.value = false"
                      >
                      <v-icon left>
                        mdi-close-circle
                      </v-icon>
                      Cerrar</v-btn>
                    </v-card-actions>
                  </v-card>
                </template>
              </v-dialog>
            </v-col>
          </v-row>
      </template>
      <br />
    </template>
    {{ actualizar }}
  </div>
</template>
<script>

import BarMultiVertical from '../components/BarMultiVertical.vue';
import LineGraph from '../components/LineGraph.vue';
import GraficoTabla from '../components/GraficoTabla.vue';
import ReporteIndicador1 from '../components/reportes/ReporteIndicador1.vue';
import html2pdf from 'html2pdf.js';
import store from "../store";
export default {
  components:{
    GraficoTabla,
    BarMultiVertical,
    LineGraph,
    ReporteIndicador1,
  },
  name: "Indicador1",
  props: {
    pglobal: {
      type:Array
    },
    pseleccion: {
      type:Array
    },
  },
  data(){
    return {
      finalGrafico1:[],
      inicialGrafico1:[],
      finalGrafico2:[],
      inicialGrafico2:[],
      finalGrafico3:[],
      inicialGrafico3:[],
      totales: {},
      global: {},
      seleccion: {},
      titulo:"",
      cabecera: ["Variables","Promedios globales","Promedios según filtro","% de variación según filtro"],
    }
  },
  created(){
    this.titulo = store.state.titulo;
    this.calcularGraficas();
    this.global = this.calcularTabla(this.pglobal);
    this.seleccion = this.calcularTabla(this.pseleccion);
    this.totales = this.tablaIndicadores();
  },
  methods:{
    imprimir(){
      html2pdf(this.$refs.documento1, {
        margin: 0,
        filename: 'consumoee.pdf',
        image: { type: 'jpeg', quality: 1 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: 'in', format: 'letter', orientation: 'landscape' }
      });
    },
    tablaIndicadores(){
      let total = {};
      total[0] = { texto:"Consumo de energía eléctrica Facturada [kWh]", promedio: this.global.indice_consumo_energetico, seleccion: this.seleccion.indice_consumo_energetico, porcentaje: Math.round((this.seleccion.indice_consumo_energetico/this.global.indice_consumo_energetico)*100)/1 };
      total[1] = { texto:"Consumo de energía eléctrica Facturada promedio por infraestructura [kWh]", promedio: this.global.avg_indice_consumo_energetico, seleccion: this.seleccion.avg_indice_consumo_energetico, porcentaje: Math.round(((this.seleccion.avg_indice_consumo_energetico/this.global.avg_indice_consumo_energetico)-1)*100)/1 };
      total[2] = { texto:"Consumo de energía primaria promedio[kWh]", promedio: this.global.avg_energia_primaria, seleccion: this.seleccion.avg_energia_primaria, porcentaje: Math.round(((this.seleccion.avg_energia_primaria/this.global.avg_energia_primaria)-1)*100)/1 };
      return total;
    },
    calcularTabla(datos){
      return {
        indice_consumo_energetico: datos.reduce((total,value)=>total+parseInt(value.indice_consumo_energetico),0),
        avg_indice_consumo_energetico: Math.round((datos.reduce((total,value)=>total+parseInt(value.avg_indice_consumo_energetico),0)/datos.length)*1)/1,
        avg_energia_primaria: Math.round((datos.reduce((total,value)=>total+parseInt(value.avg_energia_primaria),0)/datos.length)*1)/1,
      }
    },
    calcularGraficas(){
      this.inicialGrafico1 = this.pglobal.map(value=>{ 
        const container = {};
        container.serie1 = value.indice_consumo_energetico;
        container.categoria = value.abr_mes +"-"+ value.gestion;
        return container;
      });
      this.finalGrafico1 = this.pseleccion.map(value=>{ 
        const container = {};
        container.serie2 = value.indice_consumo_energetico;
        return container;
      });
      this.inicialGrafico2 = this.pglobal.map(value=>{ 
        const container = {};
        container.serie1 = parseInt(value.avg_indice_consumo_energetico);
        container.categoria = value.abr_mes +"-"+ value.gestion;
        return container;
      });
      this.finalGrafico2 = this.pseleccion.map(value=>{ 
        const container = {};
        container.serie2 = parseInt(value.avg_indice_consumo_energetico);
        return container;
      });
      this.inicialGrafico3 = [];
      this.inicialGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.inicialGrafico3.push({serie1: 0, categoria: element.categoria});
        else{
          total = ((parseFloat(this.inicialGrafico2[index].serie1)/parseFloat(this.inicialGrafico2[index-1].serie1))-1)*100;
          this.inicialGrafico3.push({serie1: Math.round(total*100)/100, categoria: element.categoria});
        }
      });
      this.finalGrafico3 = [];
      this.finalGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.finalGrafico3.push({serie2: 0});
        else{
          total = ((parseFloat(this.finalGrafico2[index].serie2)/parseFloat(this.finalGrafico2[index-1].serie2))-1)*100;
          this.finalGrafico3.push({serie2: Math.round(total*100)/100});
        }
      });
    }
  },
  
  computed: {
    actualizar: function(){
      this.calcularGraficas();
      this.global = this.calcularTabla(this.pglobal);
      this.seleccion = this.calcularTabla(this.pseleccion);
      this.totales = this.tablaIndicadores();
    }
  }

}
</script>
