<template>
  <div class="px-5" >
    <v-row class="px-5" align="center">
      <v-col class="d-flex py-0" cols="8">
        <v-select
          v-model="selectEdificio"
          :items="edificios"
          label="Infraestructura"
          @input="onEdificio($event)"
        >
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="4">
        <v-text-field
          v-model="usuarioDependencia"
          label="Dependencia/Usuario"
          disabled
        ></v-text-field>
      </v-col>
    </v-row>
    <v-row class="px-5" align="center">
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectGestionInicio"
          :items="gestionesInicio"
          label="Gestion Inicio"
          @input="onGestionInicio($event)"
        >
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectMesInicio"
          :items="mesesInicio"
          label="Mes Inicio"
          item-text="desc_mes"
          item-value="mes"
          @input="onMesInicio($event)"
          return-object
        >
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectGestionFin"
          :items="gestionesFin"
          label="Gestion Fin"
          @input="onGestionFin($event)"
        >
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectMesFin"
          :items="mesesFin"
          label="Mes Fin"
          item-text="desc_mes"
          item-value="mes"
          @input="onMesFin($event)"
          return-object
        >
        </v-select>
      </v-col>
    </v-row>
    {{ dependencia }}
  </div>
</template>

<script>
export default {
  name:"FiltroBechmark",
  props: {
    datos:{
      type: Object
    },
    pdependencia:{
      type: String
    }
  },
  data: ()=>({
    selectMesInicio: null,
    mesesInicio: [
      { "desc_mes": "ENERO","mes": 1 },
      { "desc_mes": "FEBRERO","mes": 2 },
      { "desc_mes": "MARZO", "mes": 3 },
      { "desc_mes": "ABRIL", "mes": 4 },
      { "desc_mes": "MAYO", "mes": 5 },
      { "desc_mes": "JUNIO", "mes": 6 },
      { "desc_mes": "JULIO", "mes": 7 },
      { "desc_mes": "AGOSTO", "mes": 8 },
      { "desc_mes": "SEPTIEMBRE", "mes": 9 },
      { "desc_mes": "OCTUBRE", "mes": 10 },
      { "desc_mes": "NOVIEMBRE", "mes": 11 },
      { "desc_mes": "DICIEMBRE", "mes": 12 },
    ],
    selectGestionInicio: null,
    gestionesInicio: [],
    selectMesFin: null,
    mesesFin: [
      { "desc_mes": "ENERO","mes": 1 },
      { "desc_mes": "FEBRERO","mes": 2 },
      { "desc_mes": "MARZO", "mes": 3 },
      { "desc_mes": "ABRIL", "mes": 4 },
      { "desc_mes": "MAYO", "mes": 5 },
      { "desc_mes": "JUNIO", "mes": 6 },
      { "desc_mes": "JULIO", "mes": 7 },
      { "desc_mes": "AGOSTO", "mes": 8 },
      { "desc_mes": "SEPTIEMBRE", "mes": 9 },
      { "desc_mes": "OCTUBRE", "mes": 10 },
      { "desc_mes": "NOVIEMBRE", "mes": 11 },
      { "desc_mes": "DICIEMBRE", "mes": 12 },
    ],
    selectGestionFin: null,
    gestionesFin: [],
    edificios: [],
    selectEdificio: [],
    usuarioDependencia: "",
  }),
  created() {
    if (typeof this.datos.data.meses[0]!=='undefined')
      this.selectMesInicio = {desc_mes: this.datos.data.meses[0].desc_mes, mes: this.datos.data.meses[0].mes };
    //this.mesesInicio = this.datos.data.meses;
    this.selectGestionInicio = this.datos.data.gestiones[0];
    this.gestionesInicio = this.datos.data.gestiones;
    this.selectMesFin = {desc_mes: this.datos.data.meses[this.datos.data.meses.length-1].desc_mes, mes: this.datos.data.meses[this.datos.data.meses.length-1].mes};
    //this.mesesFin = this.datos.data.meses;
    this.selectGestionFin = this.datos.data.gestiones[0];
    this.gestionesFin = this.datos.data.gestiones;
    this.selectEdificio = this.datos.data.edificio[0];
    this.edificios = this.datos.data.edificio;
    this.usuarioDependencia = this.datos.data.dependencia;
  },
  methods:{
    onMesFin(e){
      this.$emit('on_mes_fin',this.selectMesFin);
    },
    onGestionFin(e){
      this.$emit('on_gestion_fin',this.selectGestionFin);
    },
    onMesInicio(e){
      this.$emit('on_mes_inicio',this.selectMesInicio);
    },
    onGestionInicio(e){
      this.$emit('on_gestion_inicio',this.selectGestionInicio);
    },
    onEdificio(e){
      this.$emit('on_edificio',this.selectEdificio);
      //this.usuarioDependencia = this.$parent.dependencia;
    },
  },
  computed:{
    dependencia(){
      this.usuarioDependencia = this.$store.state.nivel
    }
  }
}
</script>