  <template>
  <div>
    <v-card  class="ma-0 pa-0">
    <v-card-title class="graficas white--text caption px-2 py-1" v-if="reporte">
      <span v-html="titulo"></span>
    </v-card-title>
    <v-card-text class="pa-2">
      <div>
        <apexchart type="bar" :height="alto" :options="chartOptions" :series="series"></apexchart>
      </div>
    </v-card-text>
  </v-card>
  {{dibujar}}
  </div>  
</template>
<script>
export default {
  name:"BarMultiVertical",
  props: {
    inicial:{
      type: Array
    },
    final:{
      type: Array
    },
    titulo:{
      type: String
    },
    tituloGrafico:{
      type: String
    },
    tituloX:{
      type: String
    },
    tituloY:{
      type: String
    },
    alto:{
      type: Number,
      default: 300
    },
    reporte:{
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      series: [{
        name: 'Global',
        data: this.inicial.map(value=>value.serie1)
      }, {
        name: 'Selección',
        data: this.final.map(value=>value.serie2)
      },],
      chartOptions: {
        chart: {
          type: 'bar',
          height: 300
        },
        /*title: {
          text: this.tituloGrafico,//this.datos.seleccion,
          align: 'center'
        },*/
        plotOptions: {
          bar: {
            horizontal: false,
            dataLabels: {
              orientation: 'vertical',
              position: 'center', // top, center, bottom
            },
          },
        },
        dataLabels: {
          enabled: true,
          formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
              return formatter.format(val);
          },
          offsetY: -20,
          style: {
            fontSize: '12px',
            colors: ["#000000"]
          }
        },
        stroke: {
          show: true,
          width: 2,
          colors: ['transparent']
        },
        xaxis: {
          categories: this.inicial.map(value=>value.categoria),
          title: {
            text: this.tituloX,//this.datos.tituloX,
          }
        },
        yaxis: {
          title: {
            text: this.tituloY, //this.datos.tituloY,
          },
          labels: {
            show: true,
            formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
              return formatter.format(val);
            },
          },
        },
        fill: {
          opacity: 1
        },
        tooltip: {
          y: {
            formatter: function (val) {
              return val 
            }
          }
        }
      },
    }
  },
  computed: {
    dibujar: function(){
      this.series = [{
        name: 'Global',
        data: this.inicial.map(value=>value.serie1)
      }, {
        name: 'Selección',
        data: this.final.map(value=>value.serie2)
      },];
      this.chartOptions = {
        chart: {
          type: 'bar',
          height: 300
        },
        /*title: {
          text: this.tituloGrafico,
          align: 'center'
        },*/
        plotOptions: {
          bar: {
            horizontal: false,
            dataLabels: {
              orientation: 'vertical',
              position: 'center', // top, center, bottom
            },
          },
        },
        dataLabels: {
          enabled: true,
          formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
              return formatter.format(val);
          },
          offsetY: -20,
          
          style: {
            fontSize: '12px',
            colors: ["#000000"]
          }
        },
        stroke: {
          show: true,
          width: 2,
          colors: ['transparent']
        },
        xaxis: {
          categories: this.inicial.map(value=>value.categoria),
          title: {
            text: this.tituloX,
          }
        },
        yaxis: {
          title: {
            text: this.tituloY,
          },
          labels: {
            show: true,
            formatter: function (val, opts) {
              let formatter = new Intl.NumberFormat('en-US');
              return formatter.format(val);
            },
          },
        },
        fill: {
          opacity: 1
        },
        tooltip: {
          y: {
            formatter: function (val) {
              return val
            }
          }
        }
      };
    }
  }
}
</script>