<template>
  <div>
    <v-card  class="ma-0 pa-0">
    <v-card-title class="graficas white--text caption px-2 py-1" v-if="reporte">
      {{ titulo }}
    </v-card-title>
    <template v-if="reporte">
      <v-card-text class="pa-2">
        <v-simple-table
          fixed-header
          :height="alto"
          dense
        >
          <template v-slot:default>
            <thead  style="background-color:#f00">
              <tr>
                <th class="text-left subtitle-2 font-weight-bold">
                  Descripción
                </th>
                <th class="text-left subtitle-2 font-weight-bold">
                  Cantidad
                </th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in param"
                :key="index"
              >
                <td>{{ item['datos'] }}</td>
                <td>{{ item['categorias'] }}</td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card-text>
    </template>
    <template v-else>
      <table id="customers">
        <tr>
          <th style="text-align:center">Descripción</th>
          <th style="text-align:center">Cantidad</th>
        </tr>
        <tr
          v-for="(item, index) in param"
          :key="index"
        >
          <td>{{ item['datos'] }}</td>
          <td>{{ item['categorias'] }}</td>
        </tr>
      </table>
    </template>
  </v-card>
  </div>  
</template>
<script>
  export default {
    name: "DataTable",
    props: {
      param:{
        type: Array
      },
      titulo:{
        type:String
      },
      alto:{
        type: Number,
        default: 200
      },
      reporte:{
        type: Boolean,
        default: true
      }
    },
  }
</script>
<style>
#customers {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
  font-size: 12px;
}

#customers td, #customers th {
  border: 1px solid #ddd;
  padding: 8px;
}

#customers tr:nth-child(even){background-color: #f2f2f2;}

#customers tr:hover {background-color: #ddd;}

#customers th {
  text-align: left;
  background-color: #2C3649;
  color: white;
}
</style>