<template>
  <v-app>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="7" md="7">
            <v-card class="elevation-12">
              <v-row>
                <v-col cols="12" md="7">
                  <v-card-text class="mt-6">
                    <h1 class="text-center display-1 secondary--text">
                      Iniciar sesión
                    </h1>
                    <h4 class="text-center mx-10 mt-2 mb-6 secondary--text">
                      Registrar los credenciales para ingreso al sistema
                    </h4>
                    <v-alert
                      v-if="error"
                      border="left"
                      dense
                      type="error"
                    >
                      Credenciales incorrectas
                    </v-alert>
                    <v-form class="px-6 py-4">
                      <v-text-field
                        label="Email"
                        v-model="form.email"
                        :rules="[() => !!form.email || 'Registrar email']"
                        prepend-inner-icon="mdi-account"
                        type="text"
                        color="primary">
                      </v-text-field>
                      
                      <v-text-field
                        label="Contraseña"
                        :rules="[() => !!form.password || 'Registrar contraseña']"
                        v-model="form.password"
                        prepend-inner-icon="mdi-key"
                        type="password"
                        color="primary">
                      </v-text-field>
                    </v-form>
                  </v-card-text>
                  <div class="text-center mt-3">
                    <v-btn 
                      color="primary" 
                      @click="enviar"
                      :disabled="form.password=='' || form.email==''"
                    > 
                      Ingresar
                    </v-btn>
                  </div>
                </v-col>
                <v-col cols="12" md="5" class="hidden-sm-and-down barracolor">
                  <v-card-text class="white--text mt-6">
                    <v-img
                      src="@/assets/logogra.png"
                    ></v-img>
                    <h3 class="text-center">
                      Plataforma de monitoreo de indicadores de consumo energético en 
                      infraestructuras públicas 
                    </h3>
                  </v-card-text>
                </v-col>
              </v-row>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
  </v-app>
</template>
<script>
import {mapActions,mapState} from 'vuex';

export default {
  name:'Login',
  data:()=>{
    return {
      form:{
        email:'',
        password:''
      }
    }
  },
  computed:{
    ...mapState('auth',['error']),
  },
  methods:{
    ...mapActions({
      login: 'auth/login'
    }),
    async enviar(){
      await this.login(this.form);
      if(!this.error)
        await this.$router.replace({name:'Home'});
    }
  }
}
</script>