<template>
  <div>
    <v-row>
      <v-col cols="12" class="caption">
        El siguiente apartado permite visualizar la información registrada de cada infraestructura,
        para que el administrador de su institución pueda revisar, modificar o actualizar los datos, para
        que los cambios sean registrados, se debe presionar el botón "grabar".
      </v-col>
      <v-col cols="12" class="py-0">
        <template v-if="nuevo">
          <v-select
            v-model="infraestructura.id_departamento"
            :items="ciudad"
            item-text="ciudad"
            item-value="id_departamento"
            label="Ciudad (*)"
            persistent-hint
            return-object
            single-line
          ></v-select>
        </template>
        <template v-else>
          <v-text-field
            v-model="infraestructura.ciudad"
            label="Ciudad"
            disabled
          ></v-text-field>
        </template>
      </v-col>
      <v-col cols="12" class="py-0">
        <template v-if="nuevo">
          <v-select
            v-model="infraestructura.id_institucion"
            :items="institucion"
            item-text="nombre"
            item-value="id_institucion"
            label="Entidad (*)"
            persistent-hint
            return-object
            single-line
          ></v-select>
        </template>
        <template v-else>
          <v-text-field
            v-model="infraestructura.nombre_infraestructura"
            label="Entidad"
            disabled
          ></v-text-field>
        </template>
      </v-col>
      <v-col cols="12" class="py-0">
          <v-text-field
            v-model="infraestructura.nombre_edificio"
            label="Nombre del edificio (*)"
          ></v-text-field>        
      </v-col>
      <v-col cols="12" class="py-0 caption">
        Dirección
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="infraestructura.t_via_1"
          label="T.Via"
        ></v-text-field>
      </v-col>
      <v-col cols="8" class="py-0">
        <v-text-field
          v-model="infraestructura.nombre_via"
          label="Nombre Vía Principal"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="infraestructura.numero"
          label="No."
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="infraestructura.t_via_2"
          label="T.Via"
        ></v-text-field>
      </v-col>
      <v-col cols="4" class="py-0">
        <v-text-field
          v-model="infraestructura.via_secundaria_1"
          label="Via Secundaria"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="infraestructura.t_via_3"
          label="T.Via"
        ></v-text-field>
      </v-col>
      <v-col cols="4" class="py-0">
        <v-text-field
          v-model="infraestructura.via_secundaria_2"
          label="Via Secundaria 2"
        ></v-text-field>
      </v-col>
      <v-col cols="12" class="py-0">
        <v-radio-group
          v-model="infraestructura.id_jerarquia"
          row
        >
          <v-radio
            label="Of principal / central"
            :value="2"
          ></v-radio>
          <v-radio
            label="Of dependiente"
            :value="1"
          ></v-radio>
        </v-radio-group>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" class="text-center">
        <template v-if="nuevo">
          <v-btn color="primary" @click="grabar" :disabled="!(infraestructura.id_institucion!=null && infraestructura.nombre_edificio!='' && infraestructura.id_departamento!=null)">
            Grabar
          </v-btn>
        </template>
        <template v-else>
          <v-btn color="primary" @click="grabar" :disabled="!(infraestructura.nombre_edificio!='')">
            Grabar
          </v-btn>
        </template>
      </v-col>
    </v-row>
    <br><br><br>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props:{
    dato: {
      type: Object
    }
  },
  data(){
    return {
      infraestructura:{
        id: null,
        id_departamento:null,
        id_institucion:null,
        nombre_edificio:'',
        t_via_1:null,
        nombre_via:null,
        numero:null,
        t_via_2:null,
        via_secundaria_1:null,
        t_via_3:null,
        via_secundaria_2:null,
        id_jerarquia:2,
      },
      nuevo: false,
      ciudad: {},
      institucion: {},
      estado: true,
    }
  },
  async mounted(){
    this.$store.commit('SET_LOADING',true );
    if(typeof this.dato.id === 'undefined')
    {
      let response = await axios.post("/api/nuevainfra");
      this.ciudad = response.data.ciudad;
      this.institucion = response.data.institucion;
      this.nuevo = true;
    }
    else{
      let response = await axios.post("/api/datosinfraestructura",{'id':this.dato.id});
      this.infraestructura = response.data.infraestructura[0];
      this.nuevo = false;
    }
    this.$store.commit('SET_LOADING',false );
  },
  methods:{
    async grabar(){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/grabarinfraestructura",{'infraestructura':this.infraestructura});
      this.infraestructura = response.data.infraestructura[0];
     // console.log(this.infraestructura);
      this.$parent.infraestructura = this.infraestructura;
      if(this.infraestructura.id != null){
        this.nuevo = false;
      }
      this.$store.commit('SET_LOADING',false );
    }
  },
}
</script>

