<template>
  <div>
    <v-row>
      <v-col cols="12" class="caption">
        El apartado “Medidores”, permite revisar, modificar o actualizar la información de sus medidores, pudiendo agregar uno nuevo (si corresponde) o anular/eliminar aquellos que ya no son utilizados por su(s) infraestructura(s). Para que los cambios sean registrados, se debe presionar el botón “grabar”.
      </v-col>
    </v-row>
    <template v-if="medidor">
      <v-btn
        class="mx-5 mb-3 mt-3"
        small
        color="primary"
        @click="addMedidor"
      >
        <v-icon dark>
          mdi-plus
        </v-icon> Nuevo
      </v-btn>
      <v-data-table
        dense
        :headers="headers"
        :items="medidores"
      >
        <template v-slot:item.actions="{ item }">
          <template v-if="item.estado">
            <v-btn x-small color="success" @click="cambiarEstado(item)">
              Habilitado
            </v-btn>
          </template>
          <template v-else>
            <v-btn x-small color="warning" @click="cambiarEstado(item)">
              Anulado
            </v-btn>
            <v-btn x-small color="error" @click="eliminar(item)">
              Eliminar
            </v-btn>
          </template>
        </template>
        <template v-slot:no-data>
          SIN REGISTROS
        </template>
      </v-data-table>
    </template>
    <template v-else>
      <NuevoMedidor :id_infraestructura="id_infraestructura" @on_close="onClose" />
    </template>
  </div>
</template>

<script>
import axios from "axios";
import NuevoMedidor from "./medidor/NuevoMedidor.vue";
export default {
  components:{
    NuevoMedidor
  },
  props:{
    dato: {
      type: Object
    }
  },
  data: () => ({
    search:'',
    headers: [
      { text: 'N° medidor', value: 'medidor'},
      { text: 'N° Cliente', value: 'numero_cliente'},
      { text: 'Cliente', value: 'cliente'},
      { text: 'Dirección', value: 'direccion'},
      { text: 'Acciones', value: 'actions', sortable: false },
    ],
    medidores:[],
    nuevo:"",
    id_infraestructura: null,
    medidor:true,
  }),
  async mounted () {
    this.$store.commit('SET_LOADING',true );
    let response = await axios.post("/api/medidores",{'id':this.dato.id});
    this.medidores = response.data.medidores;
    this.id_infraestructura = response.data.id_infraestructura;
    this.$store.commit('SET_LOADING',false );
  },
  methods: {
    async onClose(){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/medidores",{'id':this.dato.id});
      this.medidores = response.data.medidores;
      this.$store.commit('SET_LOADING',false );
      this.medidor = true;
    },
    async eliminar(item){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/borrarmedidor",{'id': item.id,'id_infraestructura': item.id_infraestructura});
      this.medidores = response.data.medidores;
      this.$store.commit('SET_LOADING',false );
    },
    addMedidor(){
      this.medidor = false;
    },
    async cambiarEstado(item){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/cambiarmedidor",{'id': item.id,'estado': !item.estado,'id_infraestructura': item.id_infraestructura});
      this.medidores = response.data.medidores;
      this.$store.commit('SET_LOADING',false );
    },
  }
}
</script>

