<template>
  <div class="dashboard mb-10">
    <template v-if="!loading">
      <Cabecera 
        titulo="INFORMACIÓN PÚBLICA" 
        iconoTitulo="mdi-monitor-dashboard" 
        :subtitulo="mes +' - '+ gestion"
        iconoSubtitulo="mdi-calendar-month"
        color="primary"
      />
      <v-container fluid >
        <CardDato :dato="result.data.resumen" />
        <v-row>
          <v-col cols="12" class="caption">
            El Sistema de Registro, Monitoreo, Reporte y Verificación (RMRV), es una plataforma en línea que tiene el propósito de brindar una fuente de información, de actualización mensual, sobre el desempeño energético y medioambiental de infraestructuras públicas.<br /><br /> 
            En este módulo, usted puede filtrar la información de las infraestructuras por ciudad o dependencia, y obtendrá la visualización de datos generales según la selección efectuada para el último mes disponible, (ver mes en la esquina superior derecha).
          </v-col>
        </v-row>
        <v-card flat tile width="100%" class="barracolor">
          <v-card-text class="white--text subtitle-1 d-flex justify-space-between my-2 py-2">
            FILTROS APLICABLES
          </v-card-text>
        </v-card>
        <FiltroDashboard 
          :datos="result" 
          @on_ciudad="onCiudad" 
          @on_dependencia="onDependencia"
        />

        <template v-if="actualizar">
          
          
          <v-row class="px-2">
            <v-col cols="12" md="6">
              <DataTable :param="datosCiudades" titulo="Cantidad de infraestructuras" :alto="160" />
            </v-col>
            <v-col cols="12" md="6">
              <BarVertical :datos="datosCiudades" titulo="Cantidad de infraestructuras" :alto="145" />
            </v-col>
          </v-row>
          <v-row class="px-2">
            <v-col cols="12" md="6">
              <DataTable :param="datosDependencia" titulo="Cantidad de infraestructuras por dependencia" :alto="415" />
            </v-col>
            <v-col cols="12" md="6">
              <BarHorizontal :datos="datosDependencia" titulo="Cantidad de infraestructuras por dependencia" :alto="400" />
            </v-col>
          </v-row>
          <v-card flat tile width="100%" class="barracolor">
            <v-card-text class="white--text subtitle-1 d-flex justify-space-between my-2 py-2">
              VARIABLES E INDICADORES GLOBALES (comparativos según selección)
            </v-card-text>
          </v-card>
          <v-row>
            <v-col cols="12" class="caption">
              Es esta sección se presentan las variables e indicadores energéticos de las infraestructuras registradas. 
              Las variables corresponden a datos recabados de cada infraestructura y están relacionados con el consumo y 
              costos de energía, cantidad de funcionarios y visitantes, superficie construida, entre otros. Los indicadores 
              utilizados son: [kWh/m<sup>2</sup>], [kWh/p+v]. La sección permite realizar un sistema comparativo entre el promedio del total de infraestructuras públicas registradas versus el filtro (ciudad y/o dependencia) de las infraestructuras seleccionadas.  
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12">
              <v-card>
                <v-card-text class="py-2 caption black--text">
                  <GraficoTabla :totales="totales" :cabecera="cabecera" />
                </v-card-text>
              </v-card>
            </v-col>
          </v-row>
          <v-card flat tile width="100%" class="barracolor">
            <v-card-text class="white--text subtitle-1 d-flex justify-space-between my-2 py-2">
              CARACTERÍSTICAS PRINCIPALES (de las estructuras seleccionadas)
            </v-card-text>
          </v-card>
          <v-row>
            <v-col cols="12" md="4">
              <Pie :datos="datosJerarquia" titulo="Jerarquía de las infraestructuras" />
            </v-col>
            <v-col cols="12" md="4">
              <BarHorizontal :datos="datosTipoInfraestructura" titulo="Tipo de edificación" />
            </v-col>
            <v-col cols="12" md="4">
              <BarVertical :datos="datosUso" titulo="Uso de las infraestructuras" />
            </v-col>
          </v-row>
          <v-row>
            <v-col cols="12" md="6">
              <BarVertical :datos="datosPropiedad" titulo="Tipo de propiedad de la infraestructura" />
            </v-col>
            <v-col cols="12" md="6">
              <BarVertical :datos="datosConstruccion" titulo="Año de construcción del bloque principal" />
            </v-col>
          </v-row>
          <template>
            <v-row>
              <v-col cols="auto">
                <v-dialog
                  transition="dialog-bottom-transition"
                  scrollable
                  max-width="600px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="px-6"
                      small
                      color="primary"
                      v-bind="attrs"
                      v-on="on"
                    >Reporte</v-btn>
                  </template>
                  <template v-slot:default="dialog">
                    <v-card>
                      <v-card-text>
                        <div v-show="true" style="width:1050px" ref="documento1">
                          <ReporteDashboard 
                            :datosPropiedad="datosPropiedad" 
                            :datosConstruccion="datosConstruccion" 
                            :datosJerarquia="datosJerarquia" 
                            :datosTipoInfraestructura="datosTipoInfraestructura" 
                            :totales="totales" 
                            :datosUso="datosUso" 
                            :datosDependencia="datosDependencia" 
                            :datosCiudades="datosCiudades" 
                            :fecha="mes +' - '+ gestion" 
                          />
                        </div>
                      </v-card-text>
                      <v-card-actions class="justify-end">
                        <v-btn
                          outlined
                          color="primary"
                          @click="imprimir"
                        >
                        <v-icon left>
                          mdi-file-pdf
                        </v-icon>
                        Generar PDF</v-btn>
                        <v-btn
                          outlined
                          color="error"
                          @click="dialog.value = false"
                        >
                        <v-icon left>
                          mdi-close-circle
                        </v-icon>
                        Cerrar</v-btn>
                      </v-card-actions>
                    </v-card>
                  </template>
                </v-dialog>
              </v-col>
            </v-row>
          </template>
        </template>
        <template v-else>
          <v-container fill-height fluid class="mt-10">
            <v-row align="center"
                justify="center">
                <v-col align="center">
                  <v-card
                    color="primary"
                    dark
                    width="200"
                  >
                    <v-card-text>
                      Actualizando......
                      <v-progress-linear
                        indeterminate
                        color="white"
                        class="mb-0"
                      ></v-progress-linear>
                    </v-card-text>
                  </v-card>
                </v-col>
            </v-row>
          </v-container>
        </template>
      </v-container>
    </template>
  </div>
</template>

<script>

import CardDato from "../components/CardDato";
import BarVertical from "../components/BarVertical";
import DataTable from '../components/DataTable.vue';
import BarHorizontal from "../components/BarHorizontal";
import Pie from "../components/Pie";
import axios from "axios";
import GraficoTabla from '../components/GraficoTabla.vue';
import Cabecera from '../components/cabecera/Cabecera.vue';
import FiltroDashboard from '../components/filtros/FiltroDashboard.vue';
import {mapState} from 'vuex';
import ReporteDashboard from '../components/reportes/ReporteDashboard.vue';
import html2pdf from 'html2pdf.js'

export default {
  components:{
    CardDato,
    BarVertical,
    BarHorizontal,
    Pie,
    GraficoTabla,
    DataTable,
    Cabecera,
    FiltroDashboard,
    ReporteDashboard,
  },
  name: "Home",
  data: () => ({
    gestion: null,
    mes: null,
    cabecera: ["Variables/Indicadores","Promedios globales","Promedios según filtro","% de variación según filtro"],
    selectCiudad: [],
    selectDependencia: [],
    datosCiudades: {},
    datosDependencia: {},
    datosJerarquia: {},
    datosTipoInfraestructura: {},
    datosPropiedad: {},
    datosConstruccion: {},
    datosUso: {},
    totales: {},
    global: {},
    seleccion: {},
    result: {},
    actualizar:true,
    generar:false,
  }),

  async created() {
    this.$store.commit('SET_LOADING',true );
    let response = await axios.get("/api/filtros");
    this.result = response.data;
    this.gestion = this.result.data.gestion;
    this.mes = this.result.data.mes;
    this.selectCiudad = this.result.data.ciudad;
    this.selectDependencia = this.result.data.dependencia;
    this.datosCiudades = this.result.data.ciudades;
    this.datosDependencia = this.result.data.dependencias;
    this.datosJerarquia = this.result.data.jerarquias;
    this.datosTipoInfraestructura = this.result.data.tipo_infraestructura;
    this.datosUso = this.result.data.utiliza;
    this.datosPropiedad = this.result.data.propiedad;
    this.datosConstruccion = this.result.data.construccion;
    this.global = {
      infraestructuras: Math.round(this.result.data.globales[0].infraestructuras),
      energia_primaria: Math.round(this.result.data.globales[0].energia_primaria),
      consumo_co2: Math.round(this.result.data.globales[0].consumo_co2*100)/100,
      indice_consumo_energetico: Math.round(this.result.data.globales[0].indice_consumo_energetico),
      indice_consumo_mes_m2: Math.round(this.result.data.globales[0].indice_consumo_mes_m2*100)/100,
      indice_consumo_persona: Math.round(this.result.data.globales[0].indice_consumo_persona*100)/100,
      indice_consumo_bs: Math.round(this.result.data.globales[0].indice_consumo_bs*100)/100,
      personas: Math.round(this.result.data.globales[0].personas),
      visitantes: Math.round(this.result.data.globales[0].visitantes),
      m2_construidos: Math.round(this.result.data.globales[0].m2_construidos*100)/100,
      indice_m2_persona: Math.round(this.result.data.globales[0].indice_m2_persona*100)/100,
      avg_indice_emision_co2_persona: Math.round(this.result.data.globales[0].avg_indice_emision_co2_persona*100)/100,
      co2_kwh: this.result.data.globales[0].co2_kwh,
    };
    this.seleccion = this.global;
    this.totales = this.tablaIndicadores();
    this.$store.commit('SET_LOADING', false);
  },
  computed:{
    ...mapState(['loading']),
  },
  methods: {
    imprimir(){
      this.generar = true;
      html2pdf(this.$refs.documento1, {
        margin: 0,
        filename: 'ciudad.pdf',
        image: { type: 'jpeg', quality: 1 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: 'in', format: 'letter', orientation: 'landscape' }
      }).then(
      function (pdf) {
        this.generar = false;
      }, 
      function(){
        this.generar = false;
      });
    },
    async actualizarDatos(){
      this.actualizar=false;
      let param = { 
        selectCiudad: this.selectCiudad, 
        selectDependencia: this.selectDependencia,
        gestion: this.gestion,
        mes: this.mes
      };
      let response = await axios.post("/api/filtrosactualizar",param);
      this.result = response.data;
      this.datosCiudades = this.result.data.ciudades;
      this.datosDependencia = this.result.data.dependencias;
      this.datosJerarquia = this.result.data.jerarquias;
      this.datosTipoInfraestructura = this.result.data.tipo_infraestructura;
      this.datosUso = this.result.data.utiliza;
      this.datosPropiedad = this.result.data.propiedad;
      this.datosConstruccion = this.result.data.construccion;
      this.seleccion = {
        infraestructuras: Math.round(this.result.data.seleccion[0].infraestructuras),
        energia_primaria: Math.round(this.result.data.seleccion[0].energia_primaria*100)/100,
        consumo_co2: Math.round(this.result.data.seleccion[0].consumo_co2*100)/100,
        indice_consumo_energetico: Math.round(this.result.data.seleccion[0].indice_consumo_energetico),
        indice_consumo_mes_m2: Math.round(this.result.data.seleccion[0].indice_consumo_mes_m2*100)/100,
        indice_consumo_persona: Math.round(this.result.data.seleccion[0].indice_consumo_persona*100)/100,
        indice_consumo_bs: Math.round(this.result.data.seleccion[0].indice_consumo_bs*100)/100,
        personas: Math.round(this.result.data.seleccion[0].personas),
        visitantes: Math.round(this.result.data.seleccion[0].visitantes),
        m2_construidos: Math.round(this.result.data.seleccion[0].m2_construidos*100)/100,
        indice_m2_persona: Math.round(this.result.data.seleccion[0].indice_m2_persona*100)/100,
        avg_indice_emision_co2_persona: Math.round(this.result.data.seleccion[0].avg_indice_emision_co2_persona*100)/100,
        co2_kwh: this.result.data.seleccion[0].co2_kwh,
      };
      this.totales = this.tablaIndicadores();
      this.actualizar=true;
    },
    onCiudad(selectCiudad){
      this.selectCiudad=selectCiudad;
      this.actualizarDatos();
    },
    onDependencia(selectDependencia){
        this.selectDependencia = selectDependencia;
        this.actualizarDatos();
    },
    tablaIndicadores(){
      let total = {};
      total[0] = { texto:"Infraestructuras (cantidad)", promedio: this.global.infraestructuras, seleccion: this.seleccion.infraestructuras, porcentaje: Math.round((this.seleccion.infraestructuras/this.global.infraestructuras)*10000)/100 };
      total[1] = { texto:"Consumo de energía eléctrica Facturada [kWh]", promedio: this.global.indice_consumo_energetico, seleccion: this.seleccion.indice_consumo_energetico, porcentaje: Math.round(((this.seleccion.indice_consumo_energetico/this.global.indice_consumo_energetico)-1)*10000)/100 };
      total[2] = { texto:"Consumo de energía primaria [kWh]", promedio: this.global.energia_primaria, seleccion: this.seleccion.energia_primaria, porcentaje: Math.round(((this.seleccion.energia_primaria/this.global.energia_primaria)-1)*10000)/100 };
      total[3] = { texto:"Emisiones de CO<sub>2</sub> [t<sub>eq</sub>]", promedio: this.global.consumo_co2, seleccion: this.seleccion.consumo_co2, porcentaje: Math.round(((this.seleccion.consumo_co2/this.global.consumo_co2)-1)*10000)/100 };
      total[4] = { texto:"Importe de Energía Eléctrica [Bs]", promedio: this.global.indice_consumo_bs, seleccion: this.seleccion.indice_consumo_bs, porcentaje: Math.round(((this.seleccion.indice_consumo_bs/this.global.indice_consumo_bs)-1)*10000)/100 };
      total[5] = { texto:"Personal [personas/día]", promedio: this.global.personas, seleccion: this.seleccion.personas, porcentaje: Math.round(((this.seleccion.personas/this.global.personas)-1)*10000)/100 };
      total[6] = { texto:"Visitantes [visitantes/día]", promedio: this.global.visitantes, seleccion: this.seleccion.visitantes, porcentaje: Math.round(((this.seleccion.visitantes/this.global.visitantes)-1)*10000)/100 };
      total[7] = { texto:"Superficie construida [m<sup>2</sup>]", promedio: this.global.m2_construidos, seleccion: this.seleccion.m2_construidos, porcentaje: Math.round(((this.seleccion.m2_construidos/this.global.m2_construidos)-1)*10000)/100 };
      total[8] = { texto:"Consumo específico energía eléctrica por superficie construida [kWh/m<sup>2</sup>]", promedio: this.global.indice_consumo_mes_m2, seleccion: this.seleccion.indice_consumo_mes_m2, porcentaje: Math.round(((this.seleccion.indice_consumo_mes_m2/this.global.indice_consumo_mes_m2)-1)*10000)/100 };
      total[9] = { texto:"Consumo específico energía eléctrica por personal y visitantes [kWh/p+v]", promedio: this.global.indice_consumo_persona, seleccion: this.seleccion.indice_consumo_persona, porcentaje: Math.round(((this.seleccion.indice_consumo_persona/this.global.indice_consumo_persona)-1)*10000)/100 };
      total[10] = { texto:"Densidad de Ocupación [m<sup>2</sup>/persona]", promedio: this.global.indice_m2_persona, seleccion: this.seleccion.indice_m2_persona, porcentaje: Math.round(((this.seleccion.indice_m2_persona/this.global.indice_m2_persona)-1)*10000)/100 };
      total[11] = { texto:"Emisión específica de CO<sub>2</sub> por persona [kg<sub>eq</sub>CO<sub>2</sub>/p+v]", promedio: this.global.avg_indice_emision_co2_persona, seleccion: this.seleccion.avg_indice_emision_co2_persona, porcentaje: Math.round(((this.seleccion.avg_indice_emision_co2_persona/this.global.avg_indice_emision_co2_persona)-1)*10000)/100 };
      total[12] = { 
        texto:"Emisión de CO2 por consumo de energía eléctrica facturada [CO<sub>2</sub>/kWh]", 
        promedio: this.global.co2_kwh, 
        seleccion: this.seleccion.co2_kwh, 
        porcentaje: Math.round(((this.seleccion.co2_kwh/this.global.co2_kwh)-1)*10000)/100 };
      return total;
    },
  }
}
</script>

