<template>
  <v-list
    dense
    nav
    flat
  >
    <v-list-item-group
      v-model="selectedItem"
      color="primary"
    >
      <v-list-item
        @click="drawerState=false"
        v-for="item in items"
        :key="item.title"
        :to="item.to"
        link
      >
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list-item
        @click="drawerState=false"
        to="/login"
        link
      >
        <v-list-item-icon>
          <v-icon>mdi-account</v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>Iniciar sesión</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list-item-group>
  </v-list>  
</template>
<script>
export default {
  name: "MenuPublico",
  data: () => ({ 
    selectedItem: 1,
    items: [
      { title: 'Inicio', icon: 'mdi-home', to: '/' },
      { title: 'Información pública', icon: 'mdi-view-dashboard', to: '/dashboard' },
      { title: 'Histórico', icon: 'mdi-chart-pie', to: '/historico' },
      { title: 'Georeferenciación', icon: 'mdi-map-marker', to: '/geografico' },
    ],
  }),
  computed: {
    drawerState: {
      get () { 
        return this.$store.getters.drawerState 
      },
      set (v) { 
        return this.$store.commit('toggleDrawerState', v) 
      }
    }
  },
}
</script>
