<template>
  <div>
    <v-card  class="ma-0 pa-0">
      <v-card-title class="graficas white--text caption px-2 py-1" v-if="reporte">
        <span v-html="titulo"></span>
      </v-card-title>
      <v-card-text class="pa-2">
        <table style="width:100%; font-size:10px;">
          <tr v-for="(rango, i) in rangos" :key="i">
            <td style="width:60%">
              <div :style="grafica[i][1]">
                {{ grafica[i][0] }}
              </div>
            </td>
            <td style="width:30%">{{ rango[0] }} - {{ rango[1] }}</td>
            <td style="width:10%">
              <v-btn v-if="mostrarGrafico(rango[0], rango[1])" color="primary" x-small dark>
                {{ valor }}
              </v-btn>
            </td>
          </tr>
        </table>
      </v-card-text>
    </v-card>
  </div>  
</template>

<script>
export default {
  name:"GraficoEficiencia",
  props: {
    rangos:{
      type: Array
    },
    valor: {
      type: Number
    },
    titulo: {
      type: String
    },
    reporte:{
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      grafica: [
        ['A','width:30%;height: 30px;background-color:#3D7D56;color:#fff;padding-top: 2px; padding-left: 5px;'],
        ['B','width:40%;height: 30px;background-color:#39B24F;color:#fff;padding-top: 2px; padding-left: 5px;'],
        ['C','width:50%;height: 30px;background-color:#B4D333;color:#fff;padding-top: 2px; padding-left: 5px;'],
        ['D','width:60%;height: 30px;background-color:#F7E600;color:#fff;padding-top: 2px; padding-left: 5px;'],
        ['E','width:70%;height: 30px;background-color:#FBB41B;color:#fff;padding-top: 2px; padding-left: 5px;'],
        ['F','width:80%;height: 30px;background-color:#EE7A26;color:#fff;padding-top: 2px; padding-left: 5px;'],
        ['G','width:90%;height: 30px;background-color:#EA1C25;color:#fff;padding-top: 2px; padding-left: 5px;']
      ]
    }
  },
  methods: {
    mostrarGrafico(inicio, fin){
      if (fin>=this.valor && inicio<=this.valor)
        return true;
      else
        return false;
    }
  }
}
</script>
