<template>
  <div>
    <v-card>
        <v-card-title class="graficas white--text caption px-2 py-1" v-if="reporte">
          {{ titulo }}
        </v-card-title>
        <v-card-text class="pa-2">
          <div id="chart">
            <apexchart type="bar" :height="alto" :options="chartOptions" :series="series"></apexchart>
          </div>
        </v-card-text>
    </v-card>
    {{ dibujar }}
  </div>
</template>
<script>
export default {
  name:"BarHorizontal",
  props: {
    datos:{
      type: Array
    },
    titulo:{
      type: String
    },
    alto:{
      type: Number,
      default: 200
    },
    reporte:{
      type: Boolean,
      default: true
    }
  },
  data() {
    return{
      series: [{
        name: 'Infraestructura',
        data: this.datos.map(item => item.categorias),
      }],
      chartOptions: {
        chart: {
          type: 'bar',
          toolbar: {
            show: false,
          },
          height:this.alto
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true,
            dataLabels: {
              position: 'top', // top, center, bottom
            },
            distributed: true,
          }
        },
        legend: {
          show: false
        },
        dataLabels: {
          enabled: true,
          formatter: function (val) {
            return val;
          },
          offsetY: -20,
          style: {
            fontSize: '12px',
            colors: ["#385F73"]
          }
        },
        xaxis: {
          categories: this.datos.map(item => item.datos),
        }
      }
    }
  },
  computed: {
    dibujar: function(){
      this.series= [{
        name: 'Infraestructura',
        data: this.datos.map(item => item.categorias),
      }];
      this.chartOptions= {
        chart: {
          type: 'bar',
          toolbar: {
            show: false,
          },
          height:this.alto
        },
        plotOptions: {
          bar: {
            borderRadius: 4,
            horizontal: true,
            dataLabels: {
              position: 'top', // top, center, bottom
            },
            distributed: true,
          }
        },
        legend: {
          show: false
        },
        dataLabels: {
          enabled: true,
          formatter: function (val) {
            return val;
          },
          offsetY: -20,
          style: {
            fontSize: '12px',
            colors: ["#385F73"]
          }
        },
        xaxis: {
          categories: this.datos.map(item => item.datos),
        }
      }
    }
  }
}
</script>