<template>
  <div>
    <v-card flat tile width="100%" :class="color">
      <v-card-text class="white--text subtitle-1 d-flex justify-space-between mt-12 py-2">
        <v-icon small color="white" class="mr-3">
          {{ iconoTitulo }} 
        </v-icon>
        {{ titulo }} 
        <v-spacer></v-spacer>
        <template v-if="subtitulo">
          <v-icon small color="white" class="mr-3">
            {{ iconoSubtitulo }}
          </v-icon>
          {{ subtitulo }}
        </template>
      </v-card-text>
    </v-card>  
    <v-card flat tile width="100%" v-if="false">
      <v-card-text class="subtitle-1 d-flex justify-space-between ma-0 py-0 px-5">
        <v-spacer></v-spacer>
        <v-btn
          text
          color="blue"
          to = "/infraestructura"
        >
          Agregar nueva infraestructuras
        </v-btn>
      </v-card-text>
    </v-card>  
  </div>
  
</template>
<script>
import store from '../../../src/store'
export default {
  props:{
    color:{
      type: String,
      required: true
    },
    titulo:{
      type: String,
      required: true
    },
    iconoTitulo:{
      type: String,
      required: false
    },
    subtitulo:{
      type: String,
      required: false
    },
    iconoSubtitulo:{
      type: String,
      required: false
    },
  },
  computed: {
    mostrarBoton(){
      if(store.state.auth.token !== null){
        if(store.state.auth.user.rol_id==2)
          return true;
        else
          return false;
      }
      return false
    }
  },
}
</script>
