<template>
  <v-app id="inspire">    
    <loading :active.sync="loading" 
      :is-full-page="true"
      color="#0D47A1"
      loader="dots"
    ></loading>
    <MenuGiz />
    <v-main class="pa-0 mx-0 mt-0 mb-0">
      <router-view></router-view>
    </v-main>
    <v-footer
      dark
      padless
      absolute
      class="py-0 my-0"
    >
    <v-card
      flat
      tile
      width="100%"
      color="primary" 
      class="text-center py-0 ma-0"
    >
      <v-card-text class="white--text caption ma-0 pa-3">
        CopyRight - {{ new Date().getFullYear() }} REALDATA <br/>
        
      </v-card-text>
    </v-card>
  </v-footer>
  </v-app>
</template>

<script>
import {mapState,mapActions} from 'vuex';
import store from '../src/store'
import MenuGiz from "./components/MenuGiz.vue";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
export default{
  components: { 
    MenuGiz,
    Loading
  },
  data: () => ({ 
    items: [
      { title: 'Inicio', icon: 'mdi-home', to: '/' },
      { title: 'Dashboard', icon: 'mdi-view-dashboard', to: '/dashboard' },
      { title: 'Histórico', icon: 'mdi-chart-pie', to: '/historico' },
    ],
    icons: [
      'mdi-facebook',
      'mdi-twitter',
      'mdi-linkedin',
      'mdi-instagram',
    ],
  }),
  created(){
    if(store.state.auth.token !== null){
      this.me();
    }    
  },
  computed:{
    ...mapState(['loading']),
  },
  methods: {
    ...mapActions({
      me: 'auth/me'
    }),
  },
}
</script>