<template>
  <div class="mb-10">
    <template v-if="!loading">
      <Cabecera 
        titulo="HISTÓRICO (variables/indicadores comparativos)" 
        iconoTitulo="mdi-monitor-dashboard" 
        color="primary"
      />
      <v-container fluid>
        <v-card flat tile width="100%" class="barracolor mb-5">
          <v-card-text class="white--text subtitle-1 d-flex justify-space-between my-2 py-2">
            FILTROS APLICABLES
          </v-card-text>
        </v-card>
        <FiltroHistorico 
          :datos="result" 
          @on_ciudad="onCiudad" 
          @on_mes_fin="onMesFin" 
          @on_dependencia="onDependencia"
          @on_mes_inicio="onMesInicio"
          @on_gestion_fin="onGestionFin"
          @on_gestion_inicio="onGestionInicio"
        />
        <v-row>
          <v-col cols="12" class="caption px-5">
            Esta sección permite visualizar las variables e indicadores para una secuencia de meses, ciudad y/o dependencia de las infraestructuras que el usuario seleccione con los filtros aplicables. <br/>
            La información está organizada según grupos de variables para: “Consumo de energía eléctrica”, “Emisión de CO<sub>2</sub>”, “Importe de energía eléctrica”, y, variables e indicadores relacionados con: “Superficie construida”, y, con “Personal y visitantes” en las infraestructuras. 

          </v-col>
        </v-row>
        <v-row align="center" justify="space-around" class="py-2">
          <v-btn :disabled="componente=='Indicador1'?true:false" text color="light-blue darken-4" @click="cambiarIndicador('Indicador1')">
            Consumo de energía eléctrica Facturada
          </v-btn>
          <v-btn :disabled="componente=='Indicador2'?true:false" text color="light-blue darken-4" @click="cambiarIndicador('Indicador2')">
            Emisión de CO<sub>2</sub>
          </v-btn>
          <v-btn :disabled="componente=='Indicador3'?true:false" text color="light-blue darken-4" @click="cambiarIndicador('Indicador3')">
            Importe de Energía Eléctrica
          </v-btn>
          <v-btn :disabled="componente=='Indicador4'?true:false" text color="light-blue darken-4" @click="cambiarIndicador('Indicador4')">
            Superficie construida
          </v-btn>
          <v-btn :disabled="componente=='Indicador5'?true:false" text color="light-blue darken-4" @click="cambiarIndicador('Indicador5')">
            Personal y visitantes
          </v-btn>
        </v-row>
        <template v-if="actualizar">
          <component :is="componente" v-bind="currentProperties"/>
        </template>
        <template v-else>
        <v-container fill-height fluid class="mt-10">
          <v-row align="center"
              justify="center">
              <v-col align="center">
                <v-card
                  color="primary"
                  dark
                  width="200"
                >
                  <v-card-text>
                    Actualizando......
                    <v-progress-linear
                      indeterminate
                      color="white"
                      class="mb-0"
                    ></v-progress-linear>
                  </v-card-text>
                </v-card>
              </v-col>
          </v-row>
        </v-container>
      </template>
      </v-container>
    </template>
  </div>
</template>
<script>
import axios from "axios";
import FiltroHistorico from '../components/filtros/FiltroHistorico.vue';
import Cabecera from '../components/cabecera/Cabecera.vue';
import Indicador1 from '../views/Indicador1.vue';
import Indicador2 from '../views/Indicador2.vue';
import Indicador3 from '../views/Indicador3.vue';
import Indicador4 from '../views/Indicador4.vue';
import Indicador5 from '../views/Indicador5.vue';
import {mapState} from 'vuex';

export default {
  components:{
    FiltroHistorico,
    Indicador1,
    Indicador2,
    Indicador3,
    Indicador4,
    Indicador5,
    Cabecera,
  },
  name: "Historico",
  data: ()=>({
    actualizar:true,
    componente: "Indicador1",
    selectMesInicio: null,
    mesesInicio: [],
    selectGestionInicio: null,
    gestionesInicio: [],
    selectMesFin: null,
    mesesFin: [],
    selectGestionFin: null,
    gestionesFin: [],
    selectCiudad: [],
    selectDependencia: [],
    global: [],
    seleccion: [],
    result: {},
    param: {},
  }),
  async created() {
    this.$store.commit('SET_LOADING',true );
    this.$store.commit('SET_TITULO','HISTÓRICO (variables/indicadores comparativos)' );
    let response = await axios.get("/api/filtroshistoricos");
    this.result = response.data;
    this.global = this.result.data.global;    
    this.seleccion = this.result.data.seleccion;    
    this.selectCiudad = this.result.data.ciudad;
    this.selectDependencia = this.result.data.dependencia;
    this.selectMesInicio={desc_mes: this.result.data.meses[0].desc_mes, mes: this.result.data.meses[0].mes };
    this.mesesInicio=this.result.data.meses;
    this.selectGestionInicio=this.result.data.gestiones[0];
    this.gestionesInicio=this.result.data.gestiones;
    this.selectMesFin={desc_mes: this.result.data.meses[this.result.data.meses.length-1].desc_mes, mes: this.result.data.meses[this.result.data.meses.length-1].mes};
    this.mesesFin=this.result.data.meses;
    this.selectGestionFin=this.result.data.gestiones[0];
    this.gestionesFin=this.result.data.gestiones;

    this.$store.commit('SET_LOADING',false );
  },
  computed: {
    ...mapState(['loading']),
    currentProperties () {
      return { pglobal:this.global,pseleccion:this.seleccion }
    },
  },
  methods:{
    
    async actualizarDatos(){
      this.actualizar=false;
      let param = { 
        selectCiudad: this.selectCiudad, 
        selectDependencia: this.selectDependencia,
        gestioninicio: this.selectGestionInicio,
        mesinicio: this.selectMesInicio.mes,
        gestionfin: this.selectGestionFin,
        mesfin: this.selectMesFin.mes
      };
      let response = await axios.post("/api/filtroshactualizar",param);
      this.result = response.data;
      this.global = this.result.data.global;    
      this.seleccion = this.result.data.seleccion;
      this.actualizar=true;
    },
    cambiarIndicador(indicador){
      this.componente = indicador
    },
    onCiudad(selectCiudad){
      this.selectCiudad=selectCiudad;
      this.actualizarDatos();
    },
    onDependencia(selectDependencia){
      this.selectDependencia = selectDependencia;
      this.actualizarDatos();
    },
    onMesInicio(selectMesInicio){
      this.selectMesInicio = selectMesInicio;
      this.actualizarDatos();
    },
    onGestionInicio(selectGestionInicio){
      this.selectGestionInicio = selectGestionInicio;
      this.actualizarDatos();
    },
    onMesFin(selectMesFin){
      this.selectMesFin = selectMesFin;
      this.actualizarDatos();
    },
    onGestionFin(selectGestionFin){
      this.selectGestionFin = selectGestionFin;
      this.actualizarDatos();
    },

  },

}
</script>