<template>
  <div>
    <v-row>
      <v-col cols="12" class="py-0 caption">
        Teléfonos
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="institucion.telefono_1"
          label="Fijo 1"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="institucion.telefono_2"
          label="Fijo 2"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="institucion.telefono_3"
          label="Fijo 3"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="institucion.celular_1"
          label="Celular 1"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="institucion.celular_2"
          label="Celular 2"
        ></v-text-field>
      </v-col>
      <v-col cols="2" class="py-0">
        <v-text-field
          v-model="institucion.celular_3"
          label="Celular 3"
        ></v-text-field>
      </v-col>
      <v-col cols="12" class="py-0 caption">
        Persona de contacto
      </v-col>
      <v-col cols="3" class="py-0">
        <v-text-field
          v-model="institucion.nombre_contacto"
          label="Nombre de contacto"
        ></v-text-field>
      </v-col>
      <v-col cols="3" class="py-0">
        <v-text-field
          v-model="institucion.cargo_contacto"
          label="Cargo"
        ></v-text-field>
      </v-col>
      <v-col cols="3" class="py-0">
        <v-text-field
          v-model="institucion.telefono"
          label="Teléfono"
        ></v-text-field>
      </v-col>
      <v-col cols="3" class="py-0">
        <v-text-field
          v-model="institucion.email"
          label="Email"
        ></v-text-field>
      </v-col>
      <v-col cols="12" class="py-0 caption">
        Dependencia
      </v-col>
      <v-col cols="12" sm="4" md="3" class="py-0">
        <v-select
          v-model="institucion.nivel_1"
          :items="nivel_1"
          label="Nivel 1"
          @input="onNivel1($event)"
        ></v-select>
      </v-col>
      <v-col cols="12" sm="4" md="3" class="py-0">
        <v-select
          v-model="institucion.nivel_2"
          :items="nivel_2"
          label="Nivel 2"
          @input="onNivel2($event)"
        ></v-select>
      </v-col>
      <v-col cols="12" sm="4" md="3" class="py-0">
        <v-select
          v-model="institucion.nivel_3"
          :items="nivel_3"
          label="Nivel 3"
          @input="onNivel3($event)"
        ></v-select>
      </v-col>
      <v-col cols="12" sm="4" md="3" class="py-0">
        <v-select
          v-model="institucion.nivel_4"
          :items="nivel_4"
          label="Nivel 4"
          @input="onNivel4($event)"
        ></v-select>
      </v-col>
      <v-col cols="12" sm="4" md="3" class="py-0">
        <v-select
          v-model="institucion.nivel_5"
          :items="nivel_5"
          label="Nivel 5"
        ></v-select>
      </v-col>
    </v-row>
    <v-row>
      <v-col cols="12" class="text-center">
      <v-btn
        color="primary"
        @click="grabar"
      >
       Grabar
      </v-btn>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props:{
    dato: {
      type: Object
    }
  },
  data(){
    return {
      institucion:{

      },
      dependencias:[],
      nivel_1:[],
      nivel_2:[],
      nivel_3:[],
      nivel_4:[],
      nivel_5:[],
      gobierno: 1
    }
  },
  async mounted(){
    this.$store.commit('SET_LOADING',true );
    let response = await axios.post("/api/datosinstitucion",{'id':this.dato.id});
    this.institucion = response.data.institucion[0];
    this.dependencias = response.data.dependencias;
    this.nivel_1 = this.dependencias.map(item=>item.nivel_1).filter(this.onlyUnique);

    this.nivel_2 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1)
                  .map(item=>item.nivel_2)
                  .filter(this.onlyUnique);
    
    this.nivel_3 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1 && item.nivel_2 == this.institucion.nivel_2)
    .map(item=>item.nivel_3)
    .filter(this.onlyUnique);
    
    this.nivel_4 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1 && item.nivel_2 == this.institucion.nivel_2 && item.nivel_3 == this.institucion.nivel_3)
    .map(item=>item.nivel_4)
    .filter(this.onlyUnique);

    this.nivel_5 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1 && item.nivel_2 == this.institucion.nivel_2 && item.nivel_3 == this.institucion.nivel_3 && item.nivel_4 == this.institucion.nivel_4)
    .map(item=>item.nivel_5)
    .filter(this.onlyUnique);

    this.$store.commit('SET_LOADING',false );
  },
  methods:{
    async grabar(){
      this.$store.commit('SET_LOADING',true );
      let response = await axios.post("/api/grabarinstitucion",{'institucion':this.institucion});
      this.institucion = response.data.institucion[0];
      this.$store.commit('SET_LOADING',false );
    },
    onlyUnique(value, index, self) {
      return self.indexOf(value) === index;
    },
    onNivel1(e){
      this.institucion.nivel_1 = e;
      this.nivel_2 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1)
                  .map(item=>item.nivel_2)
                  .filter(this.onlyUnique);
      this.institucion.nivel_2 = [];
      this.institucion.nivel_3 = [];
      this.institucion.nivel_4 = [];
      this.institucion.nivel_5 = [];
    },
    onNivel2(e){
      this.institucion.nivel_2 = e;
      this.nivel_3 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1 && item.nivel_2 == this.institucion.nivel_2 )
                  .map(item=>item.nivel_3)
                  .filter(this.onlyUnique);
      this.institucion.nivel_3 = [];
      this.institucion.nivel_4 = [];
      this.institucion.nivel_5 = [];
    },
    onNivel3(e){
      this.institucion.nivel_3 = e;
      this.nivel_4 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1 && item.nivel_2 == this.institucion.nivel_2 && item.nivel_3 == this.institucion.nivel_3)
                  .map(item=>item.nivel_4)
                  .filter(this.onlyUnique);
      this.institucion.nivel_4 = [];
      this.institucion.nivel_5 = [];
    },
    onNivel4(e){
      this.institucion.nivel_4 = e;
      this.nivel_5 = this.dependencias.filter(item => item.nivel_1 == this.institucion.nivel_1 && item.nivel_2 == this.institucion.nivel_2 && item.nivel_3 == this.institucion.nivel_3 && item.nivel_4 == this.institucion.nivel_4)
                  .map(item=>item.nivel_4)
                  .filter(this.onlyUnique);
      this.institucion.nivel_5 = [];
    },
  }
}
</script>

