<template>
  <div class="px-5" >
    <v-row class="px-5" align="center">
      <v-col class="d-flex py-0" cols="12" sm="6">
        <v-select
          v-model="selectCiudad"
          :items="ciudades"
          label="Ciudades"
          @input="onCiudad($event)"
          multiple
        >
          <template v-slot:selection="{ item, index }">
            <v-chip v-if="index === 0">
              <span>{{ item }}</span>
            </v-chip>
            <span
              v-if="index === 1"
              class="grey--text caption"
            >
              (+{{ selectCiudad.length - 1 }} otras)
            </span>
          </template>
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="12" sm="6">
        <v-select
          v-model="selectDependencia"
          :items="dependencias"
          label="Dependencias"
          @input="onDependencia($event)"
          multiple
        >
          <template v-slot:selection="{ item, index }">
            <v-chip v-if="index === 0">
              <span>{{ item }}</span>
            </v-chip>
            <span
              v-if="index === 1"
              class="grey--text caption"
            >
              (+{{ selectDependencia.length - 1 }} otras)
            </span>
          </template>
        </v-select>
      </v-col>
    </v-row>
    <v-row class="px-5" align="center">
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectGestionInicio"
          :items="gestionesInicio"
          label="Gestión inicio"
          @input="onGestionInicio($event)"
        >
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectMesInicio"
          :items="mesesInicio"
          label="Mes Inicio"
          item-text="desc_mes"
          item-value="mes"
          @input="onMesInicio($event)"
          return-object
        >
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectGestionFin"
          :items="gestionesFin"
          label="Gestión fin"
          @input="onGestionFin($event)"
        >
        </v-select>
      </v-col>
      <v-col class="d-flex py-0" cols="6" sm="3">
        <v-select
          v-model="selectMesFin"
          :items="mesesFin"
          label="Mes Fin"
          item-text="desc_mes"
          item-value="mes"
          @input="onMesFin($event)"
          return-object
        >
        </v-select>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name:"FiltroHistorico",
  props: {
    datos:{
      type: Object
    },
  },
  data: ()=>({
    selectMesInicio: null,
    mesesInicio: [
      { "desc_mes": "ENERO","mes": 1 },
      { "desc_mes": "FEBRERO","mes": 2 },
      { "desc_mes": "MARZO", "mes": 3 },
      { "desc_mes": "ABRIL", "mes": 4 },
      { "desc_mes": "MAYO", "mes": 5 },
      { "desc_mes": "JUNIO", "mes": 6 },
      { "desc_mes": "JULIO", "mes": 7 },
      { "desc_mes": "AGOSTO", "mes": 8 },
      { "desc_mes": "SEPTIEMBRE", "mes": 9 },
      { "desc_mes": "OCTUBRE", "mes": 10 },
      { "desc_mes": "NOVIEMBRE", "mes": 11 },
      { "desc_mes": "DICIEMBRE", "mes": 12 },
    ],
    selectGestionInicio: null,
    gestionesInicio: [],
    selectMesFin: null,
    mesesFin: [
      { "desc_mes": "ENERO","mes": 1 },
      { "desc_mes": "FEBRERO","mes": 2 },
      { "desc_mes": "MARZO", "mes": 3 },
      { "desc_mes": "ABRIL", "mes": 4 },
      { "desc_mes": "MAYO", "mes": 5 },
      { "desc_mes": "JUNIO", "mes": 6 },
      { "desc_mes": "JULIO", "mes": 7 },
      { "desc_mes": "AGOSTO", "mes": 8 },
      { "desc_mes": "SEPTIEMBRE", "mes": 9 },
      { "desc_mes": "OCTUBRE", "mes": 10 },
      { "desc_mes": "NOVIEMBRE", "mes": 11 },
      { "desc_mes": "DICIEMBRE", "mes": 12 },
    ],
    selectGestionFin: null,
    gestionesFin: [],
    ciudades: null,
    selectCiudad: [],
    dependencias: null,
    selectDependencia: [],
  }),
  created() {
    this.selectMesInicio = {desc_mes: this.datos.data.meses[0].desc_mes, mes: this.datos.data.meses[0].mes };
    //this.mesesInicio = this.datos.data.meses;
    this.selectGestionInicio = this.datos.data.gestiones[0];
    this.gestionesInicio = this.datos.data.gestiones;
    this.selectMesFin = {desc_mes: this.datos.data.meses[this.datos.data.meses.length-1].desc_mes, mes: this.datos.data.meses[this.datos.data.meses.length-1].mes};
    //this.mesesFin = this.datos.data.meses;
    //console.log(this.datos.data.meses);
    this.selectGestionFin = this.datos.data.gestiones[0];
    this.gestionesFin = this.datos.data.gestiones;
    this.ciudades = this.datos.data.ciudad;
    this.selectCiudad = this.datos.data.ciudad;
    this.dependencias = this.datos.data.dependencia;
    this.selectDependencia = this.datos.data.dependencia;
  },
  methods:{
    onMesFin(e){
      this.$emit('on_mes_fin',this.selectMesFin);
    },
    onGestionFin(e){
      this.$emit('on_gestion_fin',this.selectGestionFin);
    },
    onMesInicio(e){
      this.$emit('on_mes_inicio',this.selectMesInicio);
    },
    onGestionInicio(e){
      this.$emit('on_gestion_inicio',this.selectGestionInicio);
    },
    onDependencia(e){
      this.$emit('on_dependencia',this.selectDependencia);
    },
    onCiudad(e){
      this.$emit('on_ciudad', this.selectCiudad);
    },
  }
}
</script>