<template>
  <v-list
    dense
    nav
    flat
  >
    <v-list-item-group
      v-model="selectedItem"
      color="primary"
    >
      <v-list-item
        @click="drawerState=false"
        v-for="item in items"
        :key="item.title"
        :to="item.to"
        link
      >
        <v-list-item-icon>
          <v-icon>{{ item.icon }}</v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>{{ item.title }}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
      <v-divider></v-divider>
      <v-list-item
        @click.prevent="cerrar"
      >
        <v-list-item-icon>
          <v-icon>mdi-export</v-icon>
        </v-list-item-icon>

        <v-list-item-content>
          <v-list-item-title>Cerrar sesión</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list-item-group>
  </v-list>  
</template>
<script>
export default {
  name: "MenuPrivado",
  data: () => { 
    return{
      selectedItem: 1,
      items_adm: [
        { title: 'Inicio', icon: 'mdi-home', to: '/' },
        { title: 'Información pública', icon: 'mdi-view-dashboard', to: '/dashboard' },
        { title: 'Histórico', icon: 'mdi-chart-pie', to: '/historico' },
        { title: 'Comparación de datos', icon: 'mdi-chart-histogram', to: '/benchmark' },
        { title: 'Indicadores', icon: 'mdi-chart-timeline', to: '/indicadores' },
        { title: 'Georeferenciación', icon: 'mdi-map-marker', to: '/geografico' },
        { title: 'Crear Entidad', icon: 'mdi-office-building', to: '/crearinfraestructura' },
        { title: 'Infraestructuras', icon: 'mdi-domain', to: '/infraestructura' },
        { title: 'Registrar Usuario', icon: 'mdi-account', to: '/usuario' },
        { title: 'Modificar Contraseña', icon: 'mdi-key', to: '/modificar' },
      ],
      items_ins: [
        { title: 'Inicio', icon: 'mdi-home', to: '/' },
        { title: 'Información pública', icon: 'mdi-view-dashboard', to: '/dashboard' },
        { title: 'Histórico', icon: 'mdi-chart-pie', to: '/historico' },
        { title: 'Comparación de datos', icon: 'mdi-chart-histogram', to: '/benchmark' },
        { title: 'Indicadores', icon: 'mdi-chart-timeline', to: '/indicadores' },
        { title: 'Georeferenciación', icon: 'mdi-map-marker', to: '/geografico' },
        { title: 'Infraestructuras', icon: 'mdi-domain', to: '/infraestructura' },
        { title: 'Modificar Contraseña', icon: 'mdi-key', to: '/modificar' },
      ],
      items_ana: [
        { title: 'Inicio', icon: 'mdi-home', to: '/' },
        { title: 'Información pública', icon: 'mdi-view-dashboard', to: '/dashboard' },
        { title: 'Histórico', icon: 'mdi-chart-pie', to: '/historico' },
        { title: 'Comparación de datos', icon: 'mdi-chart-histogram', to: '/benchmark' },
        { title: 'Indicadores', icon: 'mdi-chart-timeline', to: '/indicadores' },
        { title: 'Georeferenciación', icon: 'mdi-map-marker', to: '/geografico' },
        { title: 'Modificar Contraseña', icon: 'mdi-key', to: '/modificar' },
      ],
      items: []
    } 
  },
  computed: {
    drawerState: {
      get () { 
        return this.$store.getters.drawerState 
      },
      set (v) { 
        return this.$store.commit('toggleDrawerState', v) 
      }
    }
  },
  mounted(){
    if(this.$store.state.auth.user.rol_id==1)
      this.items = this.items_adm
    else if(this.$store.state.auth.user.rol_id==2)
      this.items = this.items_ins
    else if(this.$store.state.auth.user.rol_id==3)
      this.items = this.items_ana
  },
  methods:{
    async cerrar(){
      this.drawerState = false;
      await this.$store.dispatch('auth/logout', null, { root: true });
      await this.$router.replace({ name: 'Home' }).catch(() => {  });
    },
  }
}
</script>
