<template>
  <div class="geografico">
    <template v-if="!loading">
        <Cabecera 
          titulo="GEOREFERENCIACIÓN" 
          iconoTitulo="mdi-map-search" 
          :subtitulo="mes +' - '+ gestion"
          iconoSubtitulo="mdi-calendar-month"
          color="primary"
        />
      <v-container fluid>
        <v-row>
          <v-col class="pa-5" cols="12" sm="3">
            <v-row>
              <v-col class="d-flex" cols="12">
                <v-select
                  v-model="selectCiudad"
                  :items="ciudades"
                  label="Ciudades"
                  @input="onCiudad($event)"
                >
                </v-select>
              </v-col>
              <v-col class="d-flex" cols="12">
                <v-select
                  v-model="selectDependencia"
                  :items="dependencias"
                  label="Dependencias"
                  @input="onDependencia($event)"
                  multiple
                >
                  <template v-slot:selection="{ item, index }">
                    <v-chip v-if="index === 0">
                      <span>{{ item }}</span>
                    </v-chip>
                    <span
                      v-if="index === 1"
                      class="grey--text caption"
                    >
                      (+{{ selectDependencia.length - 1 }} otras)
                    </span>
                  </template>
                </v-select>
              </v-col>
              <v-col class="d-flex" cols="12">
                <v-select
                  v-model="selectJerarquia"
                  :items="jerarquia"
                  label="Jerarquía"
                  @input="onJerarquia($event)"
                  multiple
                >
                  <template v-slot:selection="{ item, index }">
                    <v-chip v-if="index === 0">
                      <span>{{ item }}</span>
                    </v-chip>
                    <span
                      v-if="index === 1"
                      class="grey--text caption"
                    >
                      (+{{ selectJerarquia.length - 1 }} otras)
                    </span>
                  </template>
                </v-select>
              </v-col>
              <v-col class="d-flex" cols="12">
                <v-select
                  v-model="selectTipo"
                  :items="tipo"
                  label="Tipo Infraestructura"
                  @input="onTipoInfraestructura($event)"
                  multiple
                >
                  <template v-slot:selection="{ item, index }">
                    <v-chip v-if="index === 0">
                      <span>{{ item }}</span>
                    </v-chip>
                    <span
                      v-if="index === 1"
                      class="grey--text caption"
                    >
                      (+{{ selectTipo.length - 1 }} otras)
                    </span>
                  </template>
                </v-select>
              </v-col>
            </v-row>
          </v-col>
            <v-col class="pa-0" cols="12" sm="9">
              <v-card height="650">
                <v-card-text class="pa-0 ma-0">
                <div style="height: 600px; width: 100%">
                  <l-map
                    :zoom="zoom"
                    :center="center"
                    :options="mapOptions"
                    style="height: 100%"
                  >
                    <l-tile-layer
                      :url="url"
                      :attribution="attribution"
                    />
                    <template v-for="item in filtrado">
                      <l-marker :lat-lng="[item.latitud,item.longitud]" :key="item.codigo">
                        <l-popup>
                          <div>
                            {{ item.codigo }} - {{ item.nombre }}
                            <p>
                              <b>Ciudad :</b>{{ item.cod_ciudad }} - {{ item.ciudad }}<br />
                              <b>Jerarquia :</b>{{ item.jerarquia }}<br />
                              <b>Dependencia :</b>{{ item.nivel_1 }}<br />
                              <b>Tipo infraestructura :</b>{{ item.tipo_infraestructura }}<br />
                              <b>Personal trabajando :</b>{{ item.personas }} personas<br /> 
                              <b>Visitantes :</b>{{ item.visitantes }} visitantes<br />
                            </p>
                          </div>
                        </l-popup>
                      </l-marker>  
                    </template>
                  </l-map>
                </div>
                </v-card-text>
              </v-card>

            </v-col>
        </v-row>
      </v-container>
    </template>
  </div>
</template>
<script>
import axios from "axios";
import Cabecera from '../components/cabecera/Cabecera.vue';
import {mapState} from "vuex";
import { LMap, LTileLayer, LMarker, LPopup, LTooltip, LIcon  } from "vue2-leaflet"; 
import { latLng, Icon } from "leaflet"; 
import 'leaflet/dist/leaflet.css';
delete Icon.Default.prototype._getIconUrl;
Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});
export default { 
  name: "Geografico", 
  components: { 
    Cabecera,
    LMap, LTileLayer, 
    LMarker, 
    LPopup, 
    LTooltip,
    LIcon
  }, 
  data() { 
    return { 
      zoom: 7, 
      center: latLng(-16.64349439756283, -66.46642182849878), 
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', 
      attribution: '© OpenStreetMap contributors', 
      mapOptions: { 
        zoomSnap: 0.5 
      }, 
      mes: null,
      gestion: null,
      ciudades : null,
      selectCiudad : [],
      dependencias: null,
      selectDependencia: [],
      jerarquia : null,
      selectJerarquia : [],
      tipo : null,
      selectTipo : [],
      datos : [],
      filtrado: null,
    }; 
  }, 
  async created() {
    this.$store.commit('SET_LOADING',true );
    let response = await axios.get("/api/filtrosmapas");
    this.result = response.data;
    this.gestion = this.result.data.gestion;
    this.mes = this.result.data.mes;
    this.ciudades = this.result.data.ciudad;
    this.selectCiudad = this.result.data.ciudad[0];
    this.dependencias = this.result.data.dependencia;
    this.selectDependencia = this.result.data.dependencia;
    this.generales = this.result.data.datos;
    this.jerarquia = this.result.data.jerarquia;
    this.selectJerarquia = this.result.data.jerarquia;
    this.tipo = this.result.data.tipo_infraestructura;
    this.selectTipo = this.result.data.tipo_infraestructura;
    this.datos = this.result.data.datos;
    this.filtrado = this.filtro();
    this.$store.commit('SET_LOADING',false );
  },
  computed:{
    ...mapState(['loading']),
  },
  methods: { 
    filtro: function(){
      return this.generales
      .filter(({ciudad}) => this.selectCiudad.includes(ciudad))
      .filter(({nivel_1}) => this.selectDependencia.includes(nivel_1))
      .filter(({jerarquia}) => this.selectJerarquia.includes(jerarquia))
      .filter(({tipo_infraestructura}) => this.selectTipo.includes(tipo_infraestructura));
    },
    onCiudad(e) {
      this.filtrado = this.filtro();
    },
    onDependencia(e) {
      this.filtrado = this.filtro();
    },
    onJerarquia(e) {
      this.filtrado = this.filtro();
    },
    onTipoInfraestructura(e) {
      this.filtrado = this.filtro();
    },
  },
};
</script>