<template>
  <div>
    <table class="caption"  cellspacing="0" cellpadding="0" border="0" width="100%">
      <thead>
        <tr class="cabecera" style="background-color:#46853a;color:#fff;">
          <th style="width:37%">{{ cabecera[0] }}</th>
          <th style="width:13%">{{ cabecera[1] }}</th>
          <th style="width:13%">{{ cabecera[2] }}</th>
          <th style="width:12%">{{ cabecera[3] }}</th>
          <th style="width:25%"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(dato, index) in totales" :key="index" class="detalle">
          <template v-if="index==12">
            <td style="text-align:right"><span v-html="totales[index].texto"></span></td>
            <td style="text-align:right">{{ totales[index].promedio }}</td>
            <td style="text-align:right">{{ totales[index].seleccion  }}</td>
            <td style="text-align:center">{{ totales[index].porcentaje }}%</td>
          </template>
          <template v-else>
            <td style="text-align:right"><span v-html="totales[index].texto"></span></td>
            <td style="text-align:right">{{ formatoNumero(totales[index].promedio)}}</td>
            <td style="text-align:right">{{ formatoNumero(totales[index].seleccion) }}</td>
            <td style="text-align:center">{{ formatoNumero(totales[index].porcentaje) }}%</td>
          </template>
          <td>
            <table width="100%" cellspacing="0" cellpadding="0">
              <tr>
                <td style="width:50%;text-align: right; font-size:10px;font-weight: bold;">
                  <template v-if="totales[index].porcentaje<0">
                  {{ totales[index].porcentaje }}%
                  <div class="a" :style="'width:'+totales[index].porcentaje*(-0.8)+'%;'">
                  </div>
                  </template>
                </td>
                <td style="width:50%;font-size:10px;font-weight: bold;">
                  <template v-if="totales[index].porcentaje>0">
                  <div class="b" :style="'width:'+totales[index].porcentaje*(0.8)+'%;'">
                  </div>
                  {{ totales[index].porcentaje }}%
                  </template>
                </td>
              </tr>
            </table>
          </td>
        </tr>
      </tbody>
    </table>
  </div> 
</template>

<script>
export default {
  name: "GraficoTabla",
  props: {
    totales: {
      type: Object
    },
    cabecera: {
      type: Array
    }
  },
  methods:{
    formatoNumero(value){
      return new Intl.NumberFormat('en-US').format(value);
    },
  },
}
</script>

<style>
div.a {
  float:right;
  right: 0px;
  height: 22px;
  background-color:#F58662;
  color:#ffffff;
  text-align: center;
} 
div.b {
  float:left;
  right: 0px;
  height: 22px;
  background-color:#26A0FC;
  color:#ffffff;
  text-align: center;
} 
.tabla {
  border-spacing: 15px;
}
tr.cabecera th{
  padding-top: 10px; padding-bottom:10px
}

tr.detalle td{
  padding: 0px; 
  vertical-align: middle;
}
.detalle { line-height: 24px; }
</style>