import { required, email, max, confirmed } from "vee-validate/dist/rules";
import { extend } from "vee-validate";

extend("required", {
  ...required,
  message: "Este campo en requerido"
});

extend("max", {
  ...max,
  message: "Este campo no debe exceder de {length} caracteres o letras"
});

extend("email", {
  ...email,
  message: "Debe ser una email valido"
});

extend("confirmed", {
  ...confirmed,
  message: "Las contraseñas deben coincidir"
});