<template>
  <div>
    <template>
      <v-row class="px-5">
        <v-card flat tile width="100%" class="barracolor mb-5">
          <v-card-text class="white--text subtitle-1 d-flex justify-space-between py-2">
            4. SUPERFICIE CONSTRUIDA
          </v-card-text>
        </v-card>
        <v-row>
            <v-col cols="12" class="caption px-5">
              La información disponible de esta sección corresponde a la superficie construida de las infraestructuras, 
              expresada en metros cuadrados. En la columna “Promedios globales”, se encuentran los datos de las variables 
              e indicadores energéticos: [kWh/m<sup>2</sup>], [kg<sub>eq</sub> CO<sub>2</sub>/m<sup>2</sup>] y [Bs/m<sup>2</sup>], del total de infraestructuras. 
              En la columna “Promedios según filtro”, se encuentran los datos de las variables e indicadores energéticos en 
              función a los filtros seleccionados según ciudad, dependencia y/o mes, y, finalmente el 
              “% de variación según filtro” presenta la variación de la selección filtrada respecto al total. 
              Las fórmulas utilizadas se detallan en el manual de usuario.
            </v-col>
        </v-row>
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-text class="py-2 caption black--text">
                <GraficoTabla :totales="totales" :cabecera="cabecera" />
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12" md="6">  
            <GraficoEficiencia 
              titulo="Gráfico de desempeño energético en función a la emisión específica de CO<sub>2</sub> por superficie construida [kg<sub>eq</sub> CO<sub>2</sub>/m<sup>2</sup>]" 
              :valor="valorEficiencia"
              :rangos="rango1"
            />
              <v-col cols="12" class="caption px-5">
                El gráfico muestra la ubicación que tiene la selección filtrada en base a una escala promedio de desempeño energético en términos de emisiones de CO<sub>2</sub> por superficie construida [kg<sub>eq</sub> CO<sub>2</sub>/m<sup>2</sup>]. La escala está elaborada con los datos de todas las infraestructuras registradas.
              </v-col>

          </v-col>
          <v-col cols="12" md="6">
            <GraficoEficiencia 
              titulo="Gráfico de desempeño energético en función al consumo específico de energía eléctrica por superficie construida [kWh/m<sup>2</sup>]" 
              :valor="valorEficiencia2"
              :rangos="rango2"
            />
              <v-col cols="12" class="caption px-5">
                El gráfico muestra la ubicación que tiene la selección filtrada en base a una escala promedio de desempeño energético en términos de consumo de energía por superficie construida [kWh/m<sup>2</sup>]. La escala está elaborada con los datos de todas las infraestructuras registradas.
              </v-col>
          </v-col>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico2" 
              :final="finalGrafico2" 
              titulo="Curva comparativa - consumos específicos promedio de energía eléctrica por superficie construida" 
              tituloGrafico="Consumo específico promedio mensual"
              tituloX="Mes - Gestión"
              tituloY="kWh/m2 (Promedio)" />
                <v-col cols="12" class="caption px-5">
                  El gráfico de líneas muestra el comportamiento del indicador de consumo de energía por superficie construida promedio mensual, del total de las infraestructuras (global), y, de la selección realizada.
                </v-col>
          </v-col>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico3" 
              :final="finalGrafico3" 
              titulo="Variación porcentual - consumos específicos promedio de energía eléctrica por superficie construida" 
              tituloGrafico="Variación porcentual mensual"
              tituloX="Mes - Gestión"
              tituloY="Porcentaje" />
              <v-col cols="12" class="caption px-5">
                El gráfico de líneas muestra el incremento o decremento de los promedios del indicador de consumo de energía por superficie construida para el total de las infraestructuras (global), y, de la selección realizada. 
              </v-col>
          </v-col>
        </v-row>
        
        <template>
            <v-row>
              <v-col cols="auto">
                <v-dialog
                  transition="dialog-bottom-transition"
                  scrollable
                  max-width="600px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="px-6"
                      small
                      color="primary"
                      v-bind="attrs"
                      v-on="on"
                    >Reporte</v-btn>
                  </template>
                  <template v-slot:default="dialog">
                    <v-card>
                      <v-card-text>
                        <div v-show="true" style="width:1050px" ref="documento1">
                          <ReporteIndicador4
                            :totales="totales" :cabecera="cabecera"
                            :valorEficiencia="valorEficiencia"
                            :rango1="rango1"              
                            :valorEficiencia2="valorEficiencia2"
                            :rango2="rango2"              
                            :inicialGrafico2="inicialGrafico2" 
                            :finalGrafico2="finalGrafico2" 
                            :inicialGrafico3="inicialGrafico3" 
                            :finalGrafico3="finalGrafico3" 
                            :tituloImpresion="titulo"
                          />
                        </div>
                      </v-card-text>
                      <v-card-actions class="justify-end">
                        <v-btn
                          outlined
                          color="primary"
                          @click="imprimir"
                        >
                        <v-icon left>
                          mdi-file-pdf
                        </v-icon>
                        Generar PDF</v-btn>
                        <v-btn
                          outlined
                          color="error"
                          @click="dialog.value = false"
                        >
                        <v-icon left>
                          mdi-close-circle
                        </v-icon>
                        Cerrar</v-btn>
                      </v-card-actions>
                    </v-card>
                  </template>
                </v-dialog>
              </v-col>
            </v-row>
        </template>
        
      </v-row>
      <br>
      {{ actualizar }}
    </template>

  </div>
</template>
<script>

import BarMultiVertical from '../components/BarMultiVertical.vue';
import LineGraph from '../components/LineGraph.vue';
import GraficoEficiencia from '../components/GraficoEficiencia.vue';
import GraficoTabla from '../components/GraficoTabla.vue';
import ReporteIndicador4 from '../components/reportes/ReporteIndicador4.vue';
import html2pdf from 'html2pdf.js'
import store from "../store";
export default {
  components:{
    BarMultiVertical,
    LineGraph,
    GraficoEficiencia,
    GraficoTabla,
    ReporteIndicador4,
  },
  name: "Indicador4",
  props: {
    pglobal: {
      type:Array
    },
    pseleccion: {
      type:Array
    },
  },
  data(){
    return {
      rango1 : [[0, 0.42],
                [0.43, 0.72],
                [0.73, 1.14],
                [1.15, 1.78],
                [1.79, 2.96],
                [2.97, 5.76],
                [5.77, 9999]],
      rango2 : [[0, 0.46],
                [0.47, 0.79],
                [0.80, 1.28],
                [1.29, 1.99],
                [2.00, 3.27],
                [3.28, 6.24],
                [6.25, 9999]],
      valorEficiencia: 0,
      valorEficiencia2: 0,
      finalGrafico2:[],
      inicialGrafico2:[],
      finalGrafico3:[],
      inicialGrafico3:[],
      totales: {},
      global: {},
      seleccion: {},
      titulo:"",
      cabecera: ["Variables/Indicadores","Promedios globales","Promedios según filtro","% de variación según filtro"],
    }
  },
  created(){
    this.calcularGraficas();
    this.titulo = store.state.titulo;
    this.global = this.calcularTabla(this.pglobal);
    this.seleccion = this.calcularTabla(this.pseleccion);
    this.totales = this.tablaIndicadores();
    this.valorEficiencia = this.seleccion.avg_indice_emision_co2_m2;
    this.valorEficiencia2 = this.seleccion.avg_indice_consumo_mes_m2;
  },
  methods:{
    imprimir(){
      html2pdf(this.$refs.documento1, {
        margin: 0,
        filename: 'consumoee.pdf',
        image: { type: 'jpeg', quality: 1 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: 'in', format: 'letter', orientation: 'landscape' }
      });
    },
    tablaIndicadores(){
      let total = {};
      total[0] = { texto:"Superficie construida total en infraestructuras [m<sup>2</sup>]", promedio: this.global.m2_construidos, seleccion: this.seleccion.m2_construidos, porcentaje: Math.round((this.seleccion.m2_construidos/this.global.m2_construidos)*10000)/100 };
      total[1] = { texto:"Superficie construida promedio por infraestructura [m<sup>2</sup>]", promedio: this.global.avg_m2_construidos, seleccion: this.seleccion.avg_m2_construidos, porcentaje: Math.round(((this.seleccion.avg_m2_construidos/this.global.avg_m2_construidos)-1)*10000)/100 };
      total[2] = { texto:"Consumo específico energía eléctrica por superficie construida [kWh/m<sup>2</sup>]", promedio: this.global.avg_indice_consumo_mes_m2, seleccion: this.seleccion.avg_indice_consumo_mes_m2, porcentaje: Math.round(((this.seleccion.avg_indice_consumo_mes_m2/this.global.avg_indice_consumo_mes_m2)-1)*10000)/100 };
      total[3] = { texto:"Emisión específica de CO<sub>2</sub> por superficie construida [kg<sub>eq</sub>CO<sub>2</sub>/m<sup>2</sup>]", promedio: this.global.avg_indice_emision_co2_m2, seleccion: this.seleccion.avg_indice_emision_co2_m2, porcentaje: Math.round(((this.seleccion.avg_indice_emision_co2_m2/this.global.avg_indice_emision_co2_m2)-1)*10000)/100 };
      total[4] = { texto:"Importe específico energía eléctrica por superficie construida [Bs/m<sup>2</sup>]", promedio: this.global.avg_indice_consumo_bs_m2, seleccion: this.seleccion.avg_indice_consumo_bs_m2, porcentaje: Math.round(((this.seleccion.avg_indice_consumo_bs_m2/this.global.avg_indice_consumo_bs_m2)-1)*10000)/100 };
      return total;
    },
    calcularTabla(datos){
      return {
        m2_construidos: Math.round((datos.reduce((total,value)=>total+parseFloat(value.m2_construidos),0)/datos.length)*100)/100,
        avg_m2_construidos: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_m2_construidos),0)/datos.length)*100)/100,
        avg_indice_consumo_mes_m2: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_indice_consumo_mes_m2),0)/datos.length)*100)/100,
        avg_indice_emision_co2_m2: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_indice_emision_co2_m2),0)/datos.length)*100)/100,
        avg_indice_consumo_bs_m2: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_indice_consumo_bs_m2),0)/datos.length)*100)/100,
      }
    },

    calcularGraficas(){
      this.inicialGrafico2 = this.pglobal.map(value=>{ 
        const container = {};
        container.serie1 = parseFloat(value.avg_indice_consumo_mes_m2);
        container.categoria = value.abr_mes +"-"+ value.gestion;
        return container;
      });
      this.finalGrafico2 = this.pseleccion.map(value=>{ 
        const container = {};
        container.serie2 = parseFloat(value.avg_indice_consumo_mes_m2);
        return container;
      });
      this.inicialGrafico3=[];
      this.inicialGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.inicialGrafico3.push({serie1: 0, categoria: element.categoria});
        else{
          total = ((parseFloat(this.inicialGrafico2[index].serie1)/parseFloat(this.inicialGrafico2[index-1].serie1))-1)*100;
          this.inicialGrafico3.push({serie1: Math.round(total*100)/100, categoria: element.categoria});
        }
      });
      this.finalGrafico3=[];
      this.finalGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.finalGrafico3.push({serie2: 0});
        else{
          total = ((parseFloat(this.finalGrafico2[index].serie2)/parseFloat(this.finalGrafico2[index-1].serie2))-1)*100;
          this.finalGrafico3.push({serie2: Math.round(total*100)/100});
        }
      }); 
    },

    mostrarGrafico(inicio, fin){
      if (fin>=this.valorEficiencia && inicio<=this.valorEficiencia)
        return true;
      else
        return false;
    }
  },
  computed: {
    actualizar: function(){
      this.calcularGraficas();
      this.global = this.calcularTabla(this.pglobal);
      this.seleccion = this.calcularTabla(this.pseleccion);
      this.totales = this.tablaIndicadores();
      this.valorEficiencia = this.seleccion.avg_indice_emision_co2_m2;
      this.valorEficiencia2 = this.seleccion.avg_indice_consumo_mes_m2;
    }
  }

}
</script>
