<template>
  <div>
    <Cabecera 
      titulo="Modificar contraseña" 
      iconoTitulo="mdi-key" 
      color="light-blue darken-4"
    />
    <v-app>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="7" md="4">
            <v-card class="elevation-12">
              <v-row>
                <v-col cols="12">
                  <ValidationObserver ref="obs" v-slot="{ invalid, validated, handleSubmit }">
                   <v-card-text class="mt-2">
                    <h2 class="text-center mx-10 mt-2 mb-6 blue--text text--darken-4">
                      Modificar contraseña
                    </h2>
                    <center>
                    <v-img
                      src="@/assets/usuario.png"
                      max-width="100"
                    ></v-img>
                    <div class="title">
                      {{ user.name }}
                    </div>
                    <div class="title">
                      {{ user.email }}
                    </div>
                    </center>
                 
                  
                   <v-form>
                    <ValidationProvider rules="required" name="Password" vid="password" v-slot="{ errors, valid }">
                      <v-text-field
                        type="password"
                        v-model="editedItem.password"
                        :error-messages="errors"
                        :success="valid"
                        label="Contraseña"
                        required
                        prepend-inner-icon="mdi-key"
                      ></v-text-field>
                    </ValidationProvider>

                    <ValidationProvider rules="required|confirmed:password" name="Password confirmation" v-slot="{ errors, valid }">
                      <v-text-field
                        type="password"
                        v-model="editedItem.confirmation"
                        :error-messages="errors"
                        :success="valid"
                        label="Confirmar Contraseña"
                        required
                        prepend-inner-icon="mdi-key"
                      ></v-text-field>
                    </ValidationProvider>
                    </v-form>
                   </v-card-text> 

                  <div class="text-center mt-3">
                    <v-btn 
                      color="primary"
                      @click="handleSubmit(submit)" :disabled="(invalid || !validated)"
                    > 
                      Cambiar Contraseñas
                    </v-btn>
                  </div>

                 </ValidationObserver>
               
                </v-col>
              </v-row>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
  </v-app>
  </div>
</template>

<script>
import Cabecera from '../components/cabecera/Cabecera.vue';
import {
  ValidationObserver,
  ValidationProvider
} from "vee-validate";
import axios from "axios";
import {mapActions,mapState} from 'vuex';

export default {
  name:'Contrasena',
  components:{
    ValidationProvider,
    ValidationObserver
  },
  data:()=>({
      editedItem: {
        password: '',
        confirmation: '',
      },
  }),
  computed:{
    ...mapState('auth',['user']),
  },
  methods:{
      async submit() {
        try{
          let response =  await axios.post('/api/modificarpassword', this.editedItem);
          await this.$store.dispatch('auth/logout', null, { root: true });
          await this.$router.replace({ name: 'Home' }).catch(() => {  });
	}catch(error){
          console.log(error);
	}
      },
  }
}
</script>
