<template>
  <div>
    <Cabecera 
      titulo="Registro de usuarios" 
      iconoTitulo="mdi-account" 
      color="primary"
    />
    <template v-if="tabla">
      <v-data-table
        dense
        :headers="headers"
        :items="desserts"
        class="elevation-1"
        :search="search"
      >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-text-field class="text-xs-center" v-model="search" append-icon="mdi-account-search" label="busqueda" single-line hide-details></v-text-field>
          <v-spacer></v-spacer>
          <v-dialog
            v-model="dialog"
            max-width="600px"
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                dark
                class="mb-2"
                v-bind="attrs"
                v-on="on"
              >
                Nuevo
              </v-btn>
            </template>
            <template>
            <ValidationObserver ref="obs" v-slot="{ invalid, validated, handleSubmit }">
              <v-card class="elevation-12">
                <v-card-text>
                  <span class="title">{{ formTitle }}</span>
                  <v-divider></v-divider>
                </v-card-text>
                <v-card-text>
                  <v-form>
                    <ValidationProvider name="editedItem.name" rules="required" v-slot="{ errors, valid }">
                      <v-text-field
                        v-model="editedItem.name"
                        :error-messages="errors"
                        :success="valid"
                        label="Nombre Completo"
                        required
                      ></v-text-field>
                    </ValidationProvider>
                    <ValidationProvider name="email" rules="required|email" v-slot="{ errors, valid }">
                      <v-text-field
                        v-model="editedItem.email"
                        :error-messages="errors"
                        :success="valid"
                        label="E-mail"
                        required
                      ></v-text-field>
                    </ValidationProvider>
                    <template v-if="estado()">
                    <ValidationProvider rules="required" name="Password" vid="password" v-slot="{ errors, valid }">
                      <v-text-field
                        type="password"
                        v-model="editedItem.password"
                        :error-messages="errors"
                        :success="valid"
                        label="Contraseña"
                        required
                      ></v-text-field>
                    </ValidationProvider>

                    <ValidationProvider rules="required|confirmed:password" name="Password confirmation" v-slot="{ errors, valid }">
                      <v-text-field
                        type="password"
                        v-model="editedItem.confirmation"
                        :error-messages="errors"
                        :success="valid"
                        label="Confirmar Contraseña"
                        required
                      ></v-text-field>
                    </ValidationProvider>
                    </template>
                    <ValidationProvider name="select" rules="required" v-slot="{ errors, valid }">
                    <v-select
                      :items="rol"
                      item-text="rol"
                      item-value="rol_id"
                      v-model="editedItem.rol_id"
                      :error-messages="errors"
                      :success="valid"
                      label="Rol"
                      required
                    ></v-select>
                  </ValidationProvider>
                  </v-form>
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn @click="close" color="error">Cerrar</v-btn>
                  <v-btn color="primary" @click="handleSubmit(submit)" :disabled="(invalid || !validated)&&estado()"
                    >Grabar</v-btn
                  >
                </v-card-actions>
              </v-card>
            </ValidationObserver>
          </template>  
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="headline">Está seguro de eliminar el usuario?</v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                <v-btn color="blue darken-1" text @click="deleteItemConfirm">Aceptar</v-btn>
              <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)">
          mdi-pencil
        </v-icon>
        <v-icon small class="mr-2" @click="deleteItem(item)">
          mdi-delete
        </v-icon>
        <v-icon small @click="asignar(item)">
          mdi-domain
        </v-icon>
      </template>
      <template v-slot:no-data>
        SIN REGISTROS
      </template>
      </v-data-table>
    </template>
    <template v-else>
      <v-card>
        <v-card-title>
        USUARIO: {{ editedItem.name }}
        <hr />
        </v-card-title>
        <v-card-text>
          <v-container fluid>
            <v-row>
              <v-col cols="9">
                <v-combobox
                  v-model="selectInfra"
                  :items="infra"
                  label="Seleccionar Entidad"
                  multiple
                  dense
                ></v-combobox>
              </v-col>
              <v-col cols="3">
                <v-btn
                  depressed
                  color="primary"
                  @click="grabarInfra()"
                >
                  GRABAR
                </v-btn> 
                <v-btn
                  depressed
                  color="error"
                  @click="tabla = true"
                >
                  CANCELAR
                </v-btn>
              </v-col>
            </v-row>
          </v-container>
          <v-data-table
            dense
            :headers="tasignacion"
            :items="asignacion"
            class="elevation-1"
            hide-default-footer
          >
            <template v-slot:item.actions="{ item }">
              <v-icon small @click="eliminar(item)">
                mdi-delete
              </v-icon>
            </template>
            <template v-slot:no-data>
              SIN REGISTROS
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          
        </v-card-actions>
      </v-card>
    </template>



  </div>
</template>

<script>
import Cabecera from '../components/cabecera/Cabecera.vue';
import axios from "axios";
import {
  ValidationObserver,
  ValidationProvider
} from "vee-validate";
export default {
  name:"Usuario",
  components:{
    Cabecera,
    ValidationProvider,
    ValidationObserver
  },
  data: () => ({
      dialog: false,
      dialogDelete: false,
      tabla:true,
      search:'',
      headers: [
        { text: 'Nombre', value: 'name'},
        { text: 'Email', value: 'email' },
        { text: 'Rol', value: 'rol.rol' },
        { text: 'Fecha creación', value: 'created_at' },
        { text: 'Fecha modificación', value: 'updated_at' },
        { text: 'Acciones', value: 'actions', sortable: false },
      ],

      tasignacion: [
        { text: 'Entidades Asignadas', value: 'nombre', sortable: true},
        { text: 'Acciones', value: 'actions', sortable: false },
      ],
      asignacion:[],
      infra:[],
      selectInfra:[],
      rol: [
        { rol_id: 1, rol: 'Administrador' },
        { rol_id: 2, rol: 'Usuario institucional' },
        { rol_id: 3, rol: 'Usuario analista' },
      ],
      desserts: [],
      editedIndex: -1,
      name:"",
      editedItem: {
        name: '',
        email: '',
        password: '',
        confirmation: '',
        rol_id: null,
      },
      defaultItem: {
        name: '',
        email: '',
        password: '',
        confirmation: '',
        rol_id: null,
      },
    }),
    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Nuevo usuario' : 'Editar usuario'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    async created () {
      this.$store.commit('SET_LOADING',true );
      let response = await axios.get("/api/usuarios");
      this.desserts = response.data;
      this.$store.commit('SET_LOADING',false );
    },

    methods: {
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      async deleteItemConfirm () {
        try {
          this.$store.commit('SET_LOADING',true);
          await axios.delete('/api/usuarios/'+this.editedItem.id);
          let response = await axios.get("/api/usuarios");
          this.desserts = response.data;
          this.$store.commit('SET_LOADING',false);
          this.closeDelete();
        } catch (error) {
          console.log(error)
        }
      },

      async asignar(item){
        try {
          this.editedItem = Object.assign({}, item)
          this.$store.commit('SET_LOADING',true);
          let response = await axios.post('/api/asignacion',{'id_usuario':item.id});
          this.asignacion = response.data.data;
          this.infra = response.data.infraestructura;
          this.$store.commit('SET_LOADING',false);
          this.tabla = false
        } catch (error) {
          console.log(error)
        }
      },
      async close () {
        this.dialog = false
        await requestAnimationFrame(() => {
          this.$refs.obs.reset();
        });
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      async submit() {
        try {
          this.$store.commit('SET_LOADING',true );
          if (this.estado()) {
            await axios.post('/api/usuarios', this.editedItem);
          }
          else{
            await axios.put('/api/usuarios/'+this.editedItem.id, this.editedItem);
          }
          let response = await axios.get("/api/usuarios");
          this.desserts = response.data;
          this.$store.commit('SET_LOADING',false );
          this.close();
        } catch (error) {
          console.log(error);
        }
      },
      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      estado (){
        if (typeof this.editedItem.id !== 'undefined') {
          return false;
        }
        else{
          return true;
        }
      },
      async eliminar(item){
        try {
          this.$store.commit('SET_LOADING',true);
          let response = await axios.post('/api/borrar',{'id_usuario':item.id_usuario,'id_institucion':item.id_institucion,});
          this.asignacion = response.data.data;
          this.$store.commit('SET_LOADING',false);
          this.tabla = false
        } catch (error) {
          console.log(error)
        }
      },
      async grabarInfra(){
        try {
          this.$store.commit('SET_LOADING',true);
          let response = await axios.post('/api/borrarinfra',{'id_usuario':this.editedItem.id,'infra':this.selectInfra,});
          this.asignacion = response.data.data;
          this.$store.commit('SET_LOADING',false);
          this.selectInfra =[]; 
          this.tabla = false
        } catch (error) {
          console.log(error)
        }
      }
    },
}
</script>
