<template>
  <div>
    <template>
      <v-row class="px-5">
        <v-card flat tile width="100%" class="barracolor mb-5">
          <v-card-text class="white--text subtitle-1 d-flex justify-space-between py-2">
            5. PERSONAL Y VISITANTES
          </v-card-text>
        </v-card>   
        <v-row>
            <v-col cols="12" class="caption px-5">
              La información disponible de esta sección corresponde a la cantidad de funcionarios que trabajan en las 
              infraestructuras y de los visitantes que se reciben. En la columna “Promedios globales”, se encuentran 
              los datos de las variables e indicadores energéticos: [kWh/p+v], [kg<sub>eq</sub> CO<sub>2</sub>/p+v] y [Bs/p+v], 
              del total de infraestructuras. En la columna “Promedios según filtro”, se encuentran los datos de las 
              variables e indicadores energéticos en función a los filtros seleccionados según ciudad, dependencia y/o mes, 
              y, finalmente el “% de variación según filtro” presenta la variación de la selección filtrada respecto al total. 
              Las fórmulas utilizadas se detallan en el manual de usuario.
            </v-col>
        </v-row>     
        <v-row>
          <v-col cols="12">
            <v-card>
              <v-card-text class="py-2 caption black--text">
                <GraficoTabla :totales="totales" :cabecera="cabecera" />
              </v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12" md="6">
            <GraficoEficiencia 
              titulo="Gráfico de desempeño energético en función al consumo específico de energía eléctrica por persona [kWh/p+v]" 
              :valor="valorEficiencia2"
              :rangos="rango2"
            />
              <v-col cols="12" class="caption px-5">
                El gráfico muestra la ubicación que tiene la selección filtrada en base a una escala promedio de desempeño energético en términos de consumo de energía por persona (funcionarios + visitantes), [kWh/p+v]. La escala está elaborada con los datos de todas las infraestructuras registradas.
              </v-col>
          </v-col>
          <v-col cols="12" md="6">  
            <GraficoEficiencia 
              titulo="Gráfico de desempeño energético en función a la emisión específica de CO<sub>2</sub> por persona [kg<sub>eq</sub>CO<sub>2</sub>/p+v]" 
              :valor="valorEficiencia"
              :rangos="rango1"
            />
              <v-col cols="12" class="caption px-5">
                El gráfico muestra la ubicación que tiene la selección filtrada en base a una escala promedio de desempeño energético en términos de emisiones de CO<sub>2</sub> por persona (funcionarios + visitantes), [kg<sub>eq</sub> CO<sub>2</sub>/p+v]. La escala está elaborada con los datos de todas las infraestructuras registradas.
              </v-col>
          </v-col>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico2" 
              :final="finalGrafico2" 
              titulo="Curva comparativa - consumos específicos promedio de energía eléctrica por persona" 
              tituloGrafico="Consumo específico promedio mensual"
              tituloX="Mes - Gestión"
              tituloY="kWh/p+v (Promedio)" />
              <v-col cols="12" class="caption px-5">
                El gráfico de líneas muestra el comportamiento del indicador de consumo de energía por persona (funcionarios + visitantes) promedio mensual, del total de las infraestructuras (global), y, de la selección realizada.
              </v-col>
          </v-col>
          <v-col cols="12">
            <LineGraph :inicial="inicialGrafico3" 
              :final="finalGrafico3" 
              titulo="Variación porcentual - consumos específicos promedio de energía eléctrica por persona" 
              tituloGrafico="Variación porcentual mensual"
              tituloX="Mes - Gestión"
              tituloY="Porcentaje" 
              />
                <v-col cols="12" class="caption px-5">
                  El gráfico de líneas muestra el incremento o decremento de los promedios del indicador de consumo de energía por persona (funcionarios + visitantes) para el total de las infraestructuras (global), y, de la selección realizada.
                </v-col>
          </v-col>
        </v-row>
        <template>
            <v-row>
              <v-col cols="auto">
                <v-dialog
                  transition="dialog-bottom-transition"
                  scrollable
                  max-width="600px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      class="px-6"
                      small
                      color="primary"
                      v-bind="attrs"
                      v-on="on"
                    >Reporte</v-btn>
                  </template>
                  <template v-slot:default="dialog">
                    <v-card>
                      <v-card-text>
                        <div v-show="true" style="width:1050px" ref="documento1">
                          <ReporteIndicador5
                            :totales="totales" :cabecera="cabecera"
                            :valorEficiencia="valorEficiencia"
                            :rango1="rango1"              
                            :valorEficiencia2="valorEficiencia2"
                            :rango2="rango2"              
                            :inicialGrafico2="inicialGrafico2" 
                            :finalGrafico2="finalGrafico2" 
                            :inicialGrafico3="inicialGrafico3" 
                            :finalGrafico3="finalGrafico3" 
                            :tituloImpresion="titulo"
                          />
                        </div>
                      </v-card-text>
                      <v-card-actions class="justify-end">
                        <v-btn
                          outlined
                          color="primary"
                          @click="imprimir"
                        >
                        <v-icon left>
                          mdi-file-pdf
                        </v-icon>
                        Generar PDF</v-btn>
                        <v-btn
                          outlined
                          color="error"
                          @click="dialog.value = false"
                        >
                        <v-icon left>
                          mdi-close-circle
                        </v-icon>
                        Cerrar</v-btn>
                      </v-card-actions>
                    </v-card>
                  </template>
                </v-dialog>
              </v-col>
            </v-row>
        </template>
      </v-row>
      <br>
      {{ actualizar }}
    </template>
  </div>
</template>
<script>

import BarMultiVertical from '../components/BarMultiVertical.vue';
import GraficoTabla from '../components/GraficoTabla.vue';
import LineGraph from '../components/LineGraph.vue';
import GraficoEficiencia from '../components/GraficoEficiencia.vue';
import ReporteIndicador5 from '../components/reportes/ReporteIndicador5.vue';
import html2pdf from 'html2pdf.js';
import state from "../store";
import store from '../store';
export default {
  components:{
    BarMultiVertical,
    LineGraph,
    GraficoEficiencia,
    GraficoTabla,
    ReporteIndicador5,
  },
  name: "Indicador5",
  props: {
    pglobal: {
      type:Array
    },
    pseleccion: {
      type:Array
    },
  },
  data(){
    return {
      valorEficiencia: 0,
      valorEficiencia2: 0,
      rango1: [[0, 1.15],
              [1.16, 2.22],
              [2.23, 4.15],
              [4.16, 7.87],
              [7.88, 15.53],
              [15.54, 33.14],
              [33.15, 9999]],
      rango2: [[0, 1.36],
              [1.37, 2.56],
              [2.57, 4.73],
              [4.74, 8.62],
              [8.63, 16.98],
              [16.99, 35.92],
              [35.93, 9999]],
      finalGrafico2:[],
      inicialGrafico2:[],
      finalGrafico3:[],
      inicialGrafico3:[],
      totales: {},
      global: {},
      seleccion: {},
      titulo: "",
      cabecera: ["Variables/Indicadores","Promedios globales","Promedios según filtro","% de variación según filtro"],
    }
  },
  created(){
    this.calcularGraficas();
    this.titulo = store.state.titulo;
    this.global = this.calcularTabla(this.pglobal);
    this.seleccion = this.calcularTabla(this.pseleccion);
    this.totales = this.tablaIndicadores();
    this.valorEficiencia = this.seleccion.avg_indice_emision_co2_persona;
    this.valorEficiencia2 = this.seleccion.avg_indice_consumo_persona;
  },
  methods:{
    imprimir(){
      html2pdf(this.$refs.documento1, {
        margin: 0,
        filename: 'consumoee.pdf',
        image: { type: 'jpeg', quality: 1 },
        html2canvas: { scale: 2 },
        jsPDF: { unit: 'in', format: 'letter', orientation: 'landscape' }
      });
    },
    tablaIndicadores(){
      let total = {};
      total[0] = { texto:"Personal y visitantes en infraestructuras [p+v]", promedio: this.global.personas, seleccion: this.seleccion.personas, porcentaje: Math.round((this.seleccion.personas/this.global.personas)*10000)/100 };
      total[1] = { texto:"Personal y visitantes promedio por infraestructura [p+v/día]", promedio: this.global.avg_personas, seleccion: this.seleccion.avg_personas, porcentaje: Math.round(((this.seleccion.avg_personas/this.global.avg_personas)-1)*10000)/100 };
      total[2] = { texto:"Consumo específico energía eléctrica por persona [kWh/p+v]", promedio: this.global.avg_indice_consumo_persona, seleccion: this.seleccion.avg_indice_consumo_persona, porcentaje: Math.round(((this.seleccion.avg_indice_consumo_persona/this.global.avg_indice_consumo_persona)-1)*10000)/100 };
      total[3] = { texto:"Emisión específica de CO<sub>2</sub> por persona [kg<sub>eq</sub>CO<sub>2</sub>/p+v]", promedio: this.global.avg_indice_emision_co2_persona, seleccion: this.seleccion.avg_indice_emision_co2_persona, porcentaje: Math.round(((this.seleccion.avg_indice_emision_co2_persona/this.global.avg_indice_emision_co2_persona)-1)*10000)/100 };
      total[4] = { texto:"Importe específico energía eléctrica por persona [Bs/p+v]", promedio: this.global.indice_consumo_bs_persona, seleccion: this.seleccion.indice_consumo_bs_persona, porcentaje: Math.round(((this.seleccion.indice_consumo_bs_persona/this.global.indice_consumo_bs_persona)-1)*10000)/100 };
      return total;
    },
    calcularTabla(datos){
      return {
        personas: Math.round((datos.reduce((total,value)=>total+parseFloat(value.personas),0)/datos.length)*100)/100,
        avg_personas: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_personas),0)/datos.length)*100)/100,
        avg_indice_consumo_persona: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_indice_consumo_persona),0)/datos.length)*100)/100,
        avg_indice_emision_co2_persona: Math.round((datos.reduce((total,value)=>total+parseFloat(value.avg_indice_emision_co2_persona),0)/datos.length)*100)/100,
        indice_consumo_bs_persona: Math.round((datos.reduce((total,value)=>total+parseFloat(value.indice_consumo_bs_persona),0)/datos.length)*100)/100,
      }
    },
    calcularGraficas(){
      this.inicialGrafico2 = this.pglobal.map(value=>{ 
        const container = {};
        container.serie1 = parseFloat(value.avg_indice_consumo_persona);
        container.categoria = value.abr_mes +"-"+ value.gestion;
        return container;
      });
      this.finalGrafico2 = this.pseleccion.map(value=>{ 
        const container = {};
        container.serie2 = parseFloat(value.avg_indice_consumo_persona);
        return container;
      });
      this.inicialGrafico3=[];
      this.inicialGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.inicialGrafico3.push({serie1: 0, categoria: element.categoria});
        else{
          total = ((parseFloat(this.inicialGrafico2[index].serie1)/parseFloat(this.inicialGrafico2[index-1].serie1))-1)*100;
          this.inicialGrafico3.push({serie1: Math.round(total*100)/100, categoria: element.categoria});
        }
      });
      this.finalGrafico3=[];
      this.finalGrafico2.forEach((element,index,array) => {
        let total;
        if(index==0)
          this.finalGrafico3.push({serie2: 0});
        else{
          total = ((parseFloat(this.finalGrafico2[index].serie2)/parseFloat(this.finalGrafico2[index-1].serie2))-1)*100;
          this.finalGrafico3.push({serie2: Math.round(total*100)/100});
        }
      });
    },

    mostrarGrafico(inicio, fin){
      if (fin>=this.valorEficiencia && inicio<=this.valorEficiencia)
        return true;
      else
        return false;
    }

  },

  computed: {
    actualizar: function(){
      this.calcularGraficas();
      this.global = this.calcularTabla(this.pglobal);
      this.seleccion = this.calcularTabla(this.pseleccion);
      this.totales = this.tablaIndicadores();
      this.valorEficiencia = this.seleccion.avg_indice_emision_co2_persona;
      this.valorEficiencia2 = this.seleccion.avg_indice_consumo_persona;
    }
  }

}
</script>
