<template>
  <div>
      <v-card>
        <v-card-title class="graficas white--text caption px-2 py-1" v-if="reporte">
          {{ titulo }}
        </v-card-title>
        <v-card-text class="pa-2">        
            <div id="chart">
            <apexchart type="donut" :height="alto" :options="chartOptions" :series="series"></apexchart>
            </div>
        </v-card-text>
      </v-card>
      {{ dibujar }}
  </div>
</template>
<script>
export default {
  name: "Pie",
  props: {
    datos:{
      type: Array
    },
    titulo:{
      type: String
    },
    alto:{
      type: Number,
      default: 247
    },
    reporte:{
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
      series: this.datos.map(item => parseInt(item.categorias)),
      chartOptions: {
        chart: {
          type: 'donut',

        },
        legend: {
          position: 'bottom'
        },
        labels: this.datos.map(item => item.datos),
        responsive: [{
          breakpoint: 480,
          options: {
            chart: {
              width: 200
            },
            legend: {
              position: 'bottom'
            }
          }
        }]
      },
    }
  },
  computed: {
    dibujar: function(){
      this.series= this.datos.map(item => parseInt(item.categorias));
      this.chartOptions= {
        chart: {
          type: 'donut',
        },
        legend: {
          position: 'bottom'
        },
        labels: this.datos.map(item => item.datos),
        responsive: [{
          breakpoint: 480,
          options: {
            chart: {
              width: 200
            },
            legend: {
              position: 'bottom'
            }
          }
        }]
      };
    }
  }
}
</script>